import { Component } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { IResult } from '@c8y/client';
import { FormlyFieldConfig, FormlyFormOptions, ɵdefineHiddenProp } from '@ngx-formly/core';
import { find, forOwn, get, mapValues, pick } from 'lodash-es';
import { BehaviorSubject, combineLatest, from, merge, Observable, of, Subject } from 'rxjs';
import { catchError, map, shareReplay, switchMap, tap } from 'rxjs/operators';
import { AlertService } from '../alert/alert.service';
import { Permissions, Status } from '../common/index';
import { C8yJSONSchema } from '../dynamic-forms/json-schema/c8y-json-schema.service';
import { ModalService } from '../modal/modal.service';
import {
  DynamicProviderConfig,
  DynamicProviderLayoutConfig
} from './model/dynamic-provider-config.model';
import { ProviderDefinition } from './model/provider-definition.model';
import { ProviderProperties } from './model/provider-properties.model';
import { ProviderConfigurationService } from './service/provider-configuration.service';
import { ProviderDefinitionsService } from './service/provider-definitions.service';

@Component({
  selector: 'c8y-sms-gateway',
  templateUrl: './provider-configuration.component.html',
  providers: [ProviderConfigurationService, ProviderDefinitionsService]
})
export class ProviderConfigurationComponent {
  layout$: Observable<DynamicProviderLayoutConfig> = this.activatedRoute.data.pipe(
    map((config: DynamicProviderConfig) => config.layout),
    tap((layout: DynamicProviderLayoutConfig) => (this.layout = layout)),
    tap((layout: DynamicProviderLayoutConfig) => {
      this.options.formState.disabled = !this.permissions.hasAllRoles(layout.saveRoles || []);
      this.beforeSaveHook = layout.beforeSaveHook;
    })
  );

  allRoles$: Observable<string[]> = this.layout$.pipe(
    map((layout: DynamicProviderLayoutConfig) => [
      ...(layout.deleteRoles || []),
      ...(layout.saveRoles || [])
    ])
  );

  providers$: Observable<ProviderDefinition[]>;
  selectedProvider$: Observable<ProviderDefinition>;
  changeProvider$: Subject<ProviderDefinition> = new BehaviorSubject(null);
  configuration$: Observable<ProviderProperties>;
  providerInput$ = new BehaviorSubject<string>('');

  form = new FormGroup({});
  model: ProviderProperties;
  fields: FormlyFieldConfig[] = [];
  options: FormlyFormOptions = {
    formState: {
      disabled: false
    }
  };

  private reload$ = new BehaviorSubject<void>(null);
  private updatedConfiguration$ = new Subject<ProviderProperties>();
  private layout: DynamicProviderLayoutConfig;
  private beforeSaveHook: (
    model: ProviderProperties,
    fields: FormlyFieldConfig[]
  ) => Promise<ProviderProperties> | ProviderProperties;

  constructor(
    public permissions: Permissions,
    private activatedRoute: ActivatedRoute,
    private modalService: ModalService,
    private alertService: AlertService,
    private providerDefinitionsService: ProviderDefinitionsService,
    private providerConfigurationService: ProviderConfigurationService,
    private jsonschema: C8yJSONSchema
  ) {}

  ngOnInit() {
    const allProviders$: Observable<ProviderDefinition[]> = from(
      this.providerDefinitionsService.list()
    ).pipe(
      map(result => result.data),
      shareReplay(1)
    );

    this.providers$ = combineLatest(allProviders$, this.providerInput$).pipe(
      map(([providers, input]: [ProviderDefinition[], string]) =>
        input
          ? providers.filter(el => el.displayName.toLowerCase().indexOf(input.toLowerCase()) >= 0)
          : providers
      ),
      shareReplay(1)
    );

    this.configuration$ = merge(
      this.updatedConfiguration$,
      this.reload$.pipe(
        switchMap(() =>
          from(this.providerConfigurationService.detail()).pipe(catchError(() => of({} as any)))
        ),
        map(result => result.data)
      )
    ).pipe(map(this.removeEncryptedValues), shareReplay(1));

    this.selectedProvider$ = combineLatest(
      allProviders$,
      this.configuration$,
      this.changeProvider$
    ).pipe(
      tap(
        ([_, configuration, newProvider]: [
          ProviderDefinition[],
          ProviderProperties,
          ProviderDefinition
        ]) =>
          (this.model = newProvider
            ? pick(this.model, 'sms.senderName', 'sms.senderAddress')
            : configuration)
      ),
      map(
        ([providers, configuration, newProvider]: [
          ProviderDefinition[],
          ProviderProperties,
          ProviderDefinition
        ]) =>
          newProvider ||
          find(
            providers,
            (provider: ProviderDefinition) => get(configuration, 'provider') === provider.id
          )
      ),
      tap((provider: ProviderDefinition) => {
        if (provider) {
          const config: FormlyFieldConfig = this.jsonschema.toFieldConfig(get(provider, 'schema'));
          if (config.fieldGroup) {
            config.fieldGroup.forEach((fieldConfig: FormlyFieldConfig) => {
              ɵdefineHiddenProp(fieldConfig, '_keyPath', {
                key: fieldConfig.key,
                path: [fieldConfig.key]
              });

              fieldConfig.expressionProperties = {
                'templateOptions.disabled': 'formState.disabled'
              };
            });
          }
          this.fields = [config];
          this.form = new FormGroup({});
        }
      }),
      shareReplay(1)
    );
  }

  async saveProviderConfiguration() {
    const modelToSave: ProviderProperties = !!this.beforeSaveHook
      ? await this.beforeSaveHook(this.model, this.fields)
      : this.model;

    forOwn(modelToSave, (value: any, key: string) => {
      if (Array.isArray(value)) {
        modelToSave[key] = value.filter(item => !!item || item === 0);
      }
    });

    try {
      const res: IResult<ProviderProperties> = await this.providerConfigurationService.update(
        modelToSave
      );
      this.changeProvider$.next(null);
      this.updatedConfiguration$.next(res.data);
      this.alertService.success(this.layout.configurationUpdatedSuccessMsg);
      this.form.markAsPristine();
    } catch (err) {
      this.alertService.addServerFailure(err);
    }
  }

  async deleteProviderConfiguration() {
    try {
      await this.modalService.confirm(
        this.layout.deleteConfigurationModalTitle,
        this.layout.deleteConfigurationModalBody,
        Status.DANGER,
        {
          ok: this.layout.deleteConfigurationModalOkBtnLabel,
          cancel: this.layout.deleteConfigurationModalCancelBtnLabel
        }
      );
      await this.providerConfigurationService.delete();
      this.reload$.next();
      this.alertService.success(this.layout.configurationDeletedSuccessMsg);
    } catch (err) {
      if (err) {
        this.alertService.addServerFailure(err);
      }
    }
  }

  private removeEncryptedValues(configuration: ProviderProperties): ProviderProperties {
    return mapValues(configuration, value => (value === '<<Encrypted>>' ? undefined : value));
  }
}
