import { Injectable } from '@angular/core';
import { RealtimeService } from './realtime.service';
import { IEvent } from '@c8y/client';
import { RealtimeSubjectService } from './realtime-subject.service';

@Injectable()
export class EventRealtimeService extends RealtimeService<IEvent> {
  constructor(protected realtimeSubject: RealtimeSubjectService) {
    super(realtimeSubject);
  }

  protected channel(): string {
    return '/events/*';
  }
}
