import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { ConfigOption, FieldWrapper } from '@ngx-formly/core';

@Component({
  selector: 'c8y-wrapper-form-field',
  templateUrl: './form-field.wrapper.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WrapperFormField extends FieldWrapper implements OnInit {
  static readonly CONFIG: ConfigOption = {
    wrappers: [{ name: 'c8y-form-field', component: WrapperFormField }]
  };

  maxHelpBlockLength: number = 64;
  showDescriptionAsPopup: boolean;

  ngOnInit() {
    this.showDescriptionAsPopup =
      this.field.type === 'radio' || this.field.type === 'typeahead' ||
      (this.to.description && this.to.description.length > this.maxHelpBlockLength);
  }
}
