import { Component, OnInit } from '@angular/core';
import {
  BasicAuth,
  FetchClient,
  ICredentials,
  IUser,
  TenantLoginOptionType,
  UserService
} from '@c8y/client';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { AlertService } from '../alert/alert.service';
import { AppStateService } from '../common/ui-state.service';
import { UserPreferencesService } from '../common/user-preferences/user-preferences.service';
import { gettext } from '../i18n/gettext';
import { TranslateService } from '../i18n/translate.service';
import { take } from 'rxjs/operators';
import { ModalService } from '../modal/modal.service';
import { Status } from '../common/status.model';
import { GainsightService } from '../product-experience/gainsight.service';
import { CookieBannerService } from '../bootstrap/cookie-banner/cookie-banner.service';
import { LoginService } from '../login/login.service';
import { omit } from 'lodash-es';
import { PasswordService } from '../authentication/password.service';

@Component({
  selector: 'c8y-user-edit-modal',
  templateUrl: './user-edit-modal.component.html'
})
export class UserEditModalComponent implements OnInit {
  currentUser: IUser;
  lang: string;
  changedLang: string;
  loading = false;
  showProductUsageSetting: boolean = false;
  currentUsageTrackingState: boolean;
  usageTrackingState: boolean;

  constructor(
    public modal: BsModalRef,
    public user: UserService,
    public ui: AppStateService,
    private auth: BasicAuth,
    private client: FetchClient,
    private alert: AlertService,
    private translate: TranslateService,
    private userPreferences: UserPreferencesService,
    private modalService: BsModalService,
    private c8yModalService: ModalService,
    private gainsightService: GainsightService,
    private cookieBannerService: CookieBannerService,
    private loginService: LoginService,
    private passwordService: PasswordService
  ) {
    this.lang = this.ui.state.lang;
    this.modalService.onHide.pipe(take(1)).subscribe((reason: string) => {
      if (reason !== null && this.changedLang !== undefined) {
        this.translate.switchToLanguage(this.lang);
      }
    });
  }

  async ngOnInit() {
    this.updateUserInAppState();
    this.showProductUsageSetting = await this.gainsightService.canEditProductExperienceSettings();
    if (this.showProductUsageSetting) {
      this.currentUsageTrackingState =
        !(await this.gainsightService.isGainsightDisabledInUserPreferences()) &&
        this.cookieBannerService.isFunctionalCookieEnabled();
    }
  }

  onDismiss() {
    if (this.changedLang !== undefined) {
      this.translate.switchToLanguage(this.lang);
    }
    this.modal.hide();
  }

  onLanguage(lang) {
    this.changedLang = lang;
    this.translate.switchToLanguage(this.changedLang);
  }

  onProductExperience(option: boolean) {
    this.usageTrackingState = option;
  }

  async updateAndClose(user) {
    this.loading = true;

    let reloadRequired = false;

    try {
      const passwordChanged = Boolean(user.password);
      const usesBasic = this.loginService.loginMode.type === TenantLoginOptionType.BASIC;
      const isExternalUser = user.customProperties.userOrigin === 'OAUTH2';

      if (!isExternalUser && passwordChanged) {
        const currentPassword = await this.passwordService.currentPassword().toPromise();
        if ( !currentPassword ) {
          return;
        }
        await this.user.changeCurrentUserPassword(user.password, currentPassword);
        if (usesBasic) {
          this.updateCredentials(user.password);
        }
      }

      if (this.changedLang && this.changedLang !== this.lang) {
        reloadRequired = await this.persistLanguage(this.changedLang);
      }

      if (this.currentUsageTrackingState !== this.usageTrackingState) {
        await this.userPreferences.set(
          this.gainsightService.USER_PREFERENCES_KEY,
          this.usageTrackingState
        );
        this.gainsightService.setFunctionalCookie(this.usageTrackingState);
        this.usageTrackingState
          ? await this.gainsightService.loadTag(this.client.tenant)
          : await this.gainsightTrackingAppReload();
      }

      if (user.customProperties.userOrigin !== 'OAUTH2') {
        await this.user.updateCurrent(omit(user, 'password'));
        await this.updateUserInAppState();
      }
      this.modal.hide();
      this.alert.success(gettext('User saved.'));
    } catch (e) {
      if (e) {
        this.alert.addServerFailure(e);
      }
    } finally {
      this.loading = false;
      if (reloadRequired) {
        location.reload();
      }
    }
  }

  async persistLanguage(lang): Promise<boolean> {
    let shouldReload;
    try {
      await this.c8yModalService.confirm(
        gettext('Reload recommended'),
        gettext(
          'To change the language in the entire application, we recommend you to reload the page. If you have any unsaved changes, you can reload later. How would you like to proceed?'
        ),
        Status.WARNING,
        {
          ok: gettext('Reload now'),
          cancel: gettext('Reload later')
        }
      );
      this.translate.saveInLocalStorage(lang);
      await this.userPreferences.set('language', lang);
      this.lang = lang;
      shouldReload = true;
    } catch (ex) {
      this.translate.saveInLocalStorage(lang);
      await this.userPreferences.set('language', lang);
      this.lang = lang;
      shouldReload = false;
    }

    return shouldReload;
  }

  async gainsightTrackingAppReload() {
    try {
      await this.c8yModalService.confirm(
        gettext('Reload required'),
        gettext(
          'To change the tracking option in the entire application, you need to reload the page. If you have any unsaved changes, you can reload later. How would you like to proceed?'
        ),
        Status.WARNING,
        {
          ok: gettext('Reload now'),
          cancel: gettext('Reload later')
        }
      );
      location.reload();
    } catch (ex) {
      // do nothing
    }
  }

  private async updateUserInAppState() {
    const currentUserResult = await this.user.current();
    this.ui.currentUser.next(currentUserResult.data);
  }

  private updateCredentials(password: string) {
    const newCredentials: ICredentials = {
      password,
      user: this.ui.currentUser.value.id,
      tenant: this.client.tenant
    };
    this.auth.updateCredentials(newCredentials);
  }
}
