import { Component, Input, OnDestroy } from '@angular/core';
import { isEmpty, mapValues } from 'lodash-es';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { Status, statusAlert, statusIcons, StatusType } from '../common/status.model';
import { ModalLabels } from '../select-modal/select-modal.model';

/**
 * Labels to be used in the selection modal.
 * @deprecated This is going to be removed after 1007.7.0. Use ModalLabels instead.
 */
// tslint:disable-next-line: no-empty-interface
export interface ILabels extends ModalLabels {}

/**
 * Defines options to be displayed in the confirm dialog.
 */
export interface ConfirmOptions {
  [key: string]: ConfirmOption;
}

export interface ConfirmOption {
  text: string;
  checked?: boolean;
  showIf?: boolean | (() => boolean | Promise<boolean> | Observable<boolean>);
  /**
   * Will disable the current option when an option with the given key is selected.
   */
  disabledByKey?: string;
}

@Component({
  selector: 'c8y-confirm-modal',
  templateUrl: './modal-confirm.component.html'
})
export class ConfirmModalComponent implements OnDestroy {
  result: Promise<boolean | { confirmed: boolean; confirmOptions: { [key: string]: boolean } }>;
  dismiss: () => void;
  close: () => void;
  @Input() title: string;
  @Input() body: string;
  @Input() confirmOptions: ConfirmOptions;
  @Input() status: StatusType = Status.INFO;
  @Input() set labels(labels: ModalLabels) {
    const { ok = 'OK', cancel = 'Cancel' } = labels || {};
    this._labels = { ok, cancel };
  }

  get labels(): ModalLabels {
    return this._labels;
  }

  private _labels: ModalLabels;
  constructor(private modal: BsModalRef) {
    this._labels = {
      ok: 'OK',
      cancel: 'Cancel'
    };
    this.result = new Promise((resolve, reject) => {
      this.dismiss = () => {
        reject();
        this.modal.hide();
      };
      this.close = () => {
        resolve(
          isEmpty(this.confirmOptions)
            ? true
            : {
                confirmed: true,
                confirmOptions: mapValues(this.confirmOptions, option => option.checked)
              }
        );
        this.modal.hide();
      };
    });
  }

  get classIcon() {
    return `dlt-c8y-icon-${statusIcons[this.status]}`;
  }

  get classAlert() {
    return statusAlert[this.status];
  }

  ngOnDestroy() {
    this.dismiss();
  }
}
