import { Component, Output, EventEmitter } from '@angular/core';
import { clone } from 'lodash-es';
import { BsModalRef } from 'ngx-bootstrap/modal';

@Component({
  selector: 'c8y-current-password-modal',
  templateUrl: './current-password-modal.component.html'
})
export class CurrentPasswordModalComponent {
  @Output() currentPasswordEmitter = new EventEmitter<string | null>();
  password: string;

  constructor( private modal: BsModalRef) {}

  passwordConfirm() {
    this.currentPasswordEmitter.emit(this.password);
    this.modal.hide();
  }

  cancel() {
    this.currentPasswordEmitter.emit(null);
    this.modal.hide();
  }
}
