import { Injectable } from '@angular/core';
import { Realtime } from '@c8y/client';
import { Observable } from 'rxjs';
import { share } from 'rxjs/operators';
import { RealtimeMessage } from './realtime.model';

/**
 * Service (providedIn root) that ensures to only create a single realtime subscription for each channel
 */
@Injectable({ providedIn: 'root' })
export class RealtimeSubjectService {
  private subjects$ = new Map<string, Observable<RealtimeMessage<unknown>>>();

  constructor(protected realtime: Realtime) {}

  getObservableForChannel<T>(channel: string): Observable<RealtimeMessage<T>> {
    if (this.subjects$.has(channel)) {
      return this.subjects$.get(channel) as Observable<RealtimeMessage<T>>;
    }
    const observable$ = this.createObservableForChannel<T>(channel, this.realtime);
    const sharedObservable$ = observable$.pipe(share());
    this.subjects$.set(channel, sharedObservable$);

    return sharedObservable$;
  }

  protected createObservableForChannel<T>(
    channel: string,
    realtime: Realtime
  ): Observable<RealtimeMessage<T>> {
    return new Observable<RealtimeMessage<T>>(observer => {
      const realtimeSubscription = realtime.subscribe(channel, msg => {
        const data: RealtimeMessage<T> = {
          channel: msg.channel,
          data: msg.data.data,
          id: msg.id,
          realtimeAction: msg.data.realtimeAction
        };
        observer.next(data);
      });
      return {
        unsubscribe() {
          realtime.unsubscribe(realtimeSubscription);
        }
      };
    });
  }
}
