import { Component, EventEmitter, Output, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, NgForm } from '@angular/forms';
import { camelCase } from 'lodash-es';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { Column, CustomColumnConfig } from '../data-grid.model';

@Component({
  selector: 'c8y-configure-custom-column',
  templateUrl: './configure-custom-column.component.html'
})
export class ConfigureCustomColumnComponent {
  readonly SIMPLE_JSON_PATH_REGEX = /^[^.\s]+(\.\S+)*$/;

  @ViewChild('columnConfigForm', { static: false }) form: NgForm;

  @Output()
  onAddCustomColumn: EventEmitter<CustomColumnConfig> = new EventEmitter();

  formGroup: FormGroup = this.fb.group({
    header: [''],
    path: [''],
    addAnother: [false]
  });
  columns: Column[];

  constructor(public fb: FormBuilder, public modalRef: BsModalRef) {}

  comparator(val1, val2) {
    return camelCase(val1) === camelCase(val2);
  }

  addColumn(): void {
    const { header, path } = this.formGroup.value;
    this.onAddCustomColumn.emit({
      name: camelCase(header),
      header,
      path,
      custom: true,
      visible: true
    });

    if (this.formGroup.value.addAnother) {
      this.form.resetForm({ addAnother: true });
    } else {
      this.modalRef.hide();
    }
  }
}
