import { Component, OnDestroy, Input, ViewChild, ContentChild } from '@angular/core';
import { Alert } from './alert.model';
import { AlertService } from './alert.service';
import { AlertDetailsComponent } from './alert-details.component';

/**
 * Alert component allows creating alert with complex view.
 */

@Component({
  selector: 'c8y-alert',
  templateUrl: './alert.component.html'
})

export class AlertComponent implements OnDestroy, Alert {

  /**
   * The type of the alert.
   */
  @Input() type: 'success' | 'warning' | 'danger' | 'info' | 'system';
  /**
   * Function to call if user clicks on the detail button.
   */
  @Input() onDetail: () => void;
  /**
   * Function to call if user clicks on the close button.
   */
  @Input() onClose: () => void;
  /**
   * Detailed information to display.
   */
  @ViewChild('detailedData', {static: false}) detailedData;
  /**
   * The text templateRef to display message.
   */
  @ViewChild('text', {static: false}) text;
  /**
   * Content of AlertDetailsComponent is displayed as alertDetails.
   */
  @ContentChild(AlertDetailsComponent, {static: false}) containDetailsComponent: AlertDetailsComponent;
  /**
   * @ignore
   */
  private viewInitTimeout;

  /**
   * @ignore
   */
  constructor(public alertService: AlertService) {}

  /**
   * AlertComponent adds new alert to collection stored in alertService after view init.
   */
  ngAfterViewInit() {
    if (!this.containDetailsComponent) {
      this.detailedData = null;
    }
    this.viewInitTimeout = setTimeout(() =>
      this.alertService.add(this)
    );
  }

  /**
   * AlertComponent removes alert from collection stored in alertService on destroy component.
   */
  ngOnDestroy() {
    clearTimeout(this.viewInitTimeout);
    this.alertService.remove(this);
  }
}
