import { Injectable } from '@angular/core';

@Injectable({ providedIn: 'root' })
export class ColorService {

  async generateColor(data: string): Promise<string> {
    const hashedString = await this.sha256(data);
    return this.stringToColor(hashedString);
  }

  async generateColorForDatapoint(fragment: string, series: string): Promise<string> {
    return await this.generateColor(JSON.stringify({ fragment, series }));
  }

  protected async sha256(message: string): Promise<string> {
    // encode as UTF-8
    const msgBuffer = new TextEncoder().encode(message);

    // hash the message
    const hashBuffer = await crypto.subtle.digest('SHA-256', msgBuffer);

    // convert ArrayBuffer to Array
    const hashArray = Array.from(new Uint8Array(hashBuffer));

    // convert bytes to hex string
    const hashHex = hashArray.map(b => b.toString(16).padStart(2, '0')).join('');
    return hashHex;
  }

  protected stringToColor(str: string): string {
    let hash = 0;
    for (let i = 0; i < str.length; i++) {
      // tslint:disable-next-line: no-bitwise
      hash = str.charCodeAt(i) + ((hash << 5) - hash);
    }
    let color = '#';
    for (let i = 0; i < 3; i++) {
      // tslint:disable-next-line: no-bitwise
      const value = (hash >> (i * 8)) & 0xff;
      color += value.toString(16).padStart(2, '0').substring(-2);
    }
    return color;
  }
}
