import { ChangeDetectionStrategy, Component, OnInit, ViewChild } from '@angular/core';
import { ConfigOption, FieldType } from '@ngx-formly/core';
import { get } from 'lodash';
import { DropAreaComponent } from '../../drop-area';

@Component({
  selector: 'c8y-field-file',
  templateUrl: './file.type.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FieldFile extends FieldType implements OnInit {
  static readonly CONFIG: ConfigOption = {
    types: [
      {
        name: 'file',
        component: FieldFile
      }
    ]
  };

  readonly dropAreaInputs: string[] = [
    'title',
    'message',
    'icon',
    'loadingMessage',
    'alwaysShow',
    'clickToOpen',
    'loading',
    'progress',
    'maxAllowedFiles',
    'files',
    'accept'
  ];

  @ViewChild('dropArea', { static: true }) dropArea: DropAreaComponent;

  ngOnInit(): void {
    const keys = Array.isArray(this.key) ? this.key : [this.key];
    const path = keys.join('.');
    this.dropArea.files = get(this.model, path);

    this.dropAreaInputs
      .filter(input => this.to[input] !== undefined)
      .forEach(input => (this.dropArea[input] = this.to[input]));
  }
}
