// tslint:disable: max-line-length
import { gettext } from '../i18n/gettext';

export interface ValidationRules {
  pattern?: RegExp;
  message?: string;
  maxLength?: number;
  minLength?: number;
}

export enum DatapointLibraryValidationErrors {
  IS_REQUIRED = 'required',
  PATTERN = 'pattern',
  SHOULD_CONTAIN_NUMBER = 'number',
  SHOULD_BE_DEFINED = 'should-be-defined',
  GREATER_THAN_SCALE_MAX = 'greater-than-scale-max',
  LESS_THAN_SCALE_MIN = 'less-than-scale-min',
  GREATER_THAN_RANGE_MAX = 'greater-than-range-max',
  LESS_THAN_RANGE_MIN = 'less-than-range-min',
  MIN_ACTIVE_COUNT = 'minActiveCount',
  MAX_ACTIVE_COUNT = 'maxActiveCount'
}

// @dynamic
export class ValidationPattern {
  static readonly messages = {
    deviceId: gettext('Device ID must not contain spaces or slashes ("/").'),
    domain: gettext(
      'Only lowercase letters, digits and hyphens allowed in the first part of the URI. ' +
        'Must start with a letter; hyphens only allowed in the middle. Must be a valid URI.'
    ),
    email: gettext('Invalid email address.'),
    emails: gettext('Invalid email addresses.'),
    internationalPhoneNumber: gettext(
      'International phone number required, in the format +49 9 876 543 210.'
    ),
    loginAlias: gettext(
      'Login alias must not contain slashes ("/", "\\") nor ("+"), (":"), ("$") signs.'
    ),
    max: gettext('Max value: {{ max }}'),
    min: gettext('Min value: {{ min }}'),
    minLength: gettext('Should have at least {{ minLength }} characters'),
    minlength: gettext('Should have at least {{ requiredLength }} characters'),
    maxLength: gettext('Should have at most {{ maxLength }} characters'),
    maxlength: gettext('Should have at most {{ requiredLength }} characters'),
    pattern: gettext('Does not match pattern {{ requiredPattern }}'),
    opcuaBrowsePath: gettext('Invalid OPC UA URI.'),
    password: gettext(
      'Password must have at least 8 characters and no more than 32 and can only contain letters, numbers and following symbols: `~!@#$%^&*()_|+-=?;:\'",.<>{}[]\\/'
    ),
    passwordConfirm: gettext('Passwords do not match.'),
    passwordStrength: gettext('Password not good enough, password must be stronger (green).'),
    phoneNumber: gettext(
      'Invalid phone number format. Only digits, spaces, slashes ("/") and dashes ("-") allowed.'
    ),
    required: gettext('This field is required.'),
    tenantId: gettext(
      'May contain lowercase letters, digits, and hyphens. ' +
        'Must start with a letter; hyphens only allowed in the middle; minimum 2 characters.'
    ),
    urlSegment: gettext('Slash, single or double dots and white space not allowed.'),
    user: gettext(
      'Username must not contain spaces nor slashes ("/", "\\") nor ("+"), (":"), ("$") signs.'
    ),
    noDots: gettext('Dots not allowed.'),
    [DatapointLibraryValidationErrors.SHOULD_CONTAIN_NUMBER]: gettext(
      'This field must contain a number.'
    ),
    [DatapointLibraryValidationErrors.SHOULD_BE_DEFINED]: gettext('Value must be defined.'),
    [DatapointLibraryValidationErrors.GREATER_THAN_SCALE_MAX]: gettext(
      'Value must be less than scale maximum.'
    ),
    [DatapointLibraryValidationErrors.LESS_THAN_SCALE_MIN]: gettext(
      'Value must be greater than scale minimum.'
    ),
    [DatapointLibraryValidationErrors.GREATER_THAN_RANGE_MAX]: gettext(
      'Value must be less than respective maximum.'
    ),
    [DatapointLibraryValidationErrors.LESS_THAN_RANGE_MIN]: gettext(
      'Value must be greater than respective minimum.'
    )
  } as const;

  static readonly rules = {
    deviceId: {
      pattern: /^[^\s/]*$/
    },
    domain: {
      pattern: /^[a-z]+[a-z0-9-]*[a-z0-9]+\.{1}.+$/
    },
    groupName: {
      maxLength: 254
    },
    hostname: {
      pattern: /^(([a-zA-Z0-9]|[a-zA-Z0-9][a-zA-Z0-9-]*[a-zA-Z0-9])\.)*([A-Za-z0-9]|[A-Za-z0-9][A-Za-z0-9-]*[A-Za-z0-9])$/
    },
    ip: {
      pattern: /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/
    },
    opcuaBrowsePath: {
      pattern: /^(opc.tcp|http|https):\/\/[^ "]+$/
    },
    password: {
      pattern: /^[a-zA-Z0-9`~!@#$%^&*()_|+\-=?;:'",.<>{}[\]\\/]{8,32}$/
    },
    phoneNumber: {
      pattern: /^$|^(\+|0{2})[\d\s/-]{1,30}$/
    },
    tenantId: {
      pattern: /^[a-z]+[a-z0-9-]*[a-z0-9]+$/
    },
    user: {
      pattern: /^[^\\/\s$:+]*$/
    },
    urlSegment: {
      pattern: /^(([.]{1,2})|([/])+|(([/]*)([.]{1,2})([/]*))|((.*)([/]|[ ]+)(.*))|(\s+))$/
    },
    loginAlias: {
      pattern: /^[^\\/$:+]*$/
    },
    noDots: {
      pattern: /^[^.]*$/
    }
  };

  static get(key: string) {
    const pattern = this.rules[key];
    return pattern as ValidationRules;
  }
}
