import { ChangeDetectionStrategy, Component } from '@angular/core';
import { ConfigOption, FieldType } from '@ngx-formly/core';

@Component({
  selector: 'c8y-field-radio',
  templateUrl: './radio.type.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FieldRadio extends FieldType {
  static readonly CONFIG: ConfigOption = {
    types: [
      {
        name: 'radio',
        component: FieldRadio,
        wrappers: ['c8y-form-field']
      }
    ]
  };

  defaultOptions = {
    templateOptions: {
      options: [],
      formCheck: 'custom' // 'custom' | 'custom-inline' | 'stacked' | 'inline'
    }
  };
}
