import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CollapseModule } from 'ngx-bootstrap/collapse';
import { DynamicComponentComponent } from './dynamic-component.component';
import { DynamicComponentService } from './dynamic-component.service';
import { I18nModule } from '../i18n/i18n.module';
import { RESOLVING_COMPONENT_WAIT_TIME } from './dynamic-component.model';

@NgModule({
  declarations: [DynamicComponentComponent],
  imports: [CommonModule, CollapseModule, RouterModule.forChild([]), I18nModule],
  exports: [DynamicComponentComponent],
  providers: [DynamicComponentService, { provide: RESOLVING_COMPONENT_WAIT_TIME, useValue: 5000 }]
})
export class DynamicComponentModule {}
