import {
  Component,
  Input,
  Output,
  EventEmitter,
  OnInit
} from '@angular/core';
import { IUser, UserService, TenantLoginOptionsService, TenantService } from '@c8y/client';
import { clone } from 'lodash-es';
import { AppStateService } from '../common/ui-state.service';
import { TranslateService } from '../i18n/translate.service';
import { NewPassword } from '../authentication/password.model';
import { BsModalService } from 'ngx-bootstrap/modal';
import { UserTotpSetupComponent } from './user-totp-setup.component';
import { AlertService } from '../alert/alert.service';
import { UserPreferencesService } from '../common/user-preferences/user-preferences.service';

export interface User extends IUser {
  phone: string;
  sendPasswordResetEmail: boolean;
  newsletter?: boolean;
}

@Component({
  selector: 'c8y-user-edit',
  templateUrl: './user-edit.component.html'
})
export class UserEditComponent implements OnInit {
  @Input() lang: string;
  @Input() loading: boolean = false;
  @Input() set user(u: User) {
    if (u) {
      this._user = clone(u);
      this.userIsExternal = u.customProperties.userOrigin === 'OAUTH2';
      this.isPhoneRequired = this.isPhoneRequired && u.twoFactorAuthenticationEnabled;
    }
  }
  get user() {
    return this._user;
  }
  @Input() showProductUsageSetting: boolean = false;
  @Input() isUsageTrackingEnabled: boolean = true;
  @Output() onUser: EventEmitter<User> = new EventEmitter();
  @Output() onLanguage: EventEmitter<string> = new EventEmitter();
  @Output() onProductExperience: EventEmitter<boolean> = new EventEmitter();
  @Output() onCancel: EventEmitter<void> = new EventEmitter();
  userHasActiveTotp = false;
  userCanSetupTotp = false;
  isPhoneRequired = false;
  userIsExternal: boolean;
  isTfaEnabled: boolean;

  private _user: User;

  constructor(
    public state: AppStateService,
    public translate: TranslateService,
    private bsModalService: BsModalService,
    private alert: AlertService,
    private userService: UserService,
    private tenantLoginOptionsService: TenantLoginOptionsService,
    private tenantService: TenantService,
    private userPreferencesService: UserPreferencesService
  ) {}

  async ngOnInit() {
    const currentTenant = (await this.tenantService.current()).data;
    const { enabledOnSystemLevel, enabledOnTenantLevel } = await this.tenantService.getTfaSettings(
      currentTenant
    );
    this.isTfaEnabled = enabledOnSystemLevel || enabledOnTenantLevel;

    await this.initializeTotpSettings();
    if (this.user.twoFactorAuthenticationEnabled && !this.userCanSetupTotp) {
      this.isPhoneRequired = true;
    }
  }

  get langs() {
    return this.state.state.langs;
  }

  setupTotp() {
    this.bsModalService.show(UserTotpSetupComponent, { class: 'modal-sm' });
    this.cancel(); // to close the user edit modal and prevent console errors on logout
  }

  cancel() {
    this.onCancel.emit();
  }

  async save() {
    if (this.loading) {
      return;
    }

    if (this.showProductUsageSetting) {
      this.onProductExperience.emit(this.isUsageTrackingEnabled);
      this.userPreferencesService.set('gainsightEnabled', this.isUsageTrackingEnabled);
    }
    this.onUser.emit(this._user);
  }

  onNewPasswordChanged(newPassword: NewPassword) {
    this._user.password = newPassword.password;
  }

  private async initializeTotpSettings() {
    try {
      this.userCanSetupTotp = await this.canUserSetupTotp();
      if (this.userCanSetupTotp) {
        const { data: totpActivity } = await this.userService.getActivityTotp();
        this.userHasActiveTotp = totpActivity.isActive;
      }
    } catch (ex) {
      this.alert.removeLastDanger();
    }
  }

  private async canUserSetupTotp() {
    // we don't check for tenant options here due to permission restrictions on that end-point
    const loginOptions = (await this.tenantLoginOptionsService.listForCurrentTenant()).data;
    return loginOptions.some(({ tfaStrategy = '' }) => tfaStrategy.toLowerCase() === 'totp');
  }
}
