import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { IIdentified } from '@c8y/client';
import { ConfigOption, FieldType } from '@ngx-formly/core';
import { TranslateService } from '@ngx-translate/core';
import { pick, get } from 'lodash-es';
import { defer, isObservable, of, pipe, UnaryFunction } from 'rxjs';
import { catchError, map, startWith, switchMap, tap } from 'rxjs/operators';
import { gettext } from '../../i18n';

@Component({
  selector: 'c8y-typeahead-type',
  templateUrl: './typeahead.type.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TypeaheadTypeComponent extends FieldType implements OnInit {
  static readonly CONFIG: ConfigOption = {
    types: [{ name: 'typeahead', component: TypeaheadTypeComponent }]
  };

  filterPipe: UnaryFunction<unknown, unknown>;
  pattern: string;
  selected: IIdentified;
  labelProp: string;
  match: boolean = false;

  placeholder$ = defer(() => of(this.to?.placeholder)).pipe(
    switchMap(placeholder =>
      placeholder
        ? of(placeholder)
        : this.defaultPlaceholder$.pipe(
            startWith(this.translateService.instant(gettext('Start typing to search')))
          )
    )
  );

  defaultPlaceholder$ = defer(() =>
    isObservable(this.to?.c8yForOptions) ? this.to?.c8yForOptions : of(this.to?.c8yForOptions)
  ).pipe(
    map(({ data }) => get(data[0], this.labelProp || 'name')),
    map(example => {
      return !!example
        ? this.translateService.instant(
            gettext('Start typing to search, for example, {{ example }}'),
            { example }
          )
        : this.translateService.instant(gettext('No items'));
    })
  );

  private valueProps: string[];
  private excludeLabelProp: boolean = false;

  constructor(private cdRef: ChangeDetectorRef, private translateService: TranslateService) {
    super();
  }

  ngOnInit() {
    if (this.to) {
      if (this.to.excludeDisplayProperty) {
        this.excludeLabelProp = this.to.excludeDisplayProperty;
      }

      if (this.to.displayProperty) {
        this.setPipe('');
        this.labelProp = this.to.displayProperty;
        this.valueProps = this.to.valueProperties;
      } else {
        console.error(
          'To correctly use the typeahead select you need to specify displayProperty: string within templateOptions!'
        );
      }
    }
  }

  selectOption(opt) {
    if (this.valueProps && this.valueProps.length > 0) {
      const pickList: string[] = this.excludeLabelProp
        ? this.valueProps
        : [...this.valueProps, this.labelProp];
      this.formControl.setValue(pick(opt, pickList));
      this.selected = { [this.labelProp]: opt[this.labelProp] };
    } else {
      this.formControl.setValue(opt);
    }
  }

  setPipe(filterStr: string) {
    this.pattern = filterStr;
    this.filterPipe = pipe(
      map((data: any[]) => {
        return data.filter(
          (el: any) =>
            el[this.labelProp] &&
            el[this.labelProp].toLowerCase().indexOf(filterStr.toLowerCase()) > -1
        );
      }),
      tap(data => {
        this.match = data.length > 0;
        this.cdRef.detectChanges();
      })
    );
  }
}
