import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { WizardComponent } from './wizard.component';
import { WizardService } from './wizard.service';

@Component({
  selector: 'c8y-wizard-header',
  template: `
    <ng-template #headerContent>
      <div class="modal-header dialog-header animated fadeIn">
        <ng-content></ng-content>
      </div>
    </ng-template>
  `
})
export class WizardHeaderComponent implements OnInit {
  @ViewChild('headerContent', { static: true }) headerContent: TemplateRef<any>;

  constructor(private wizard: WizardComponent, private wizardService: WizardService) {}

  ngOnInit() {
    setTimeout(() => {
      this.wizardService.headerTemplate$.next({
        template: this.headerContent,
        wizardId: this.wizard.id
      });
    });
  }
}
