import { Component, ContentChild, HostBinding } from '@angular/core';
import { ListItemComponent } from './list-item.component';

/**
 * Allows to display a timeline. Should be used with
 * content projection.
 *
 * @example
 * ```html
 * <c8y-list-group>
 *   <c8y-li-timeline>
 *     24.April 2020
 *     <c8y-li>
 *       Hello
 *     </c8y-li>
 *   </c8y-li-timeline>
 * </c8y-list-group>
 * ```
 */
@Component({
  selector: 'c8y-list-item-timeline, c8y-li-timeline',
  templateUrl: './list-item-timeline.component.html',
  host: {
    class: 'c8y-list--timeline__item'
  }
})
export class ListItemTimelineComponent {
  @ContentChild(ListItemComponent, { static: false }) listItemComponent: ListItemComponent;
  @HostBinding('class.expanded') get expanded() {
    return !this.listItemComponent?.collapsed;
  }
}
