import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  TemplateRef,
  ViewChild
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { filter } from 'rxjs/operators';
import { Wizard, WizardConfig, WizardEntry } from './wizard.model';
import { WizardService } from './wizard.service';

/**
 * A generic component that is intended to serve as the building block of more wizards.
 *
 * **Example**
 * ```html
 * <button
 *    title="Add application"
 *    class="btn btn-primary"
 *    (click)="addApplication()"
 *  >
 *    Add application
 *  </button>
 * ```
 *
 * ```ts
 * import { Component } from '@angular/core';
 * import { WizardConfig, WizardService, Wizard } from '@c8y/ngx-components';
 * import { ModalOptions } from 'ngx-bootstrap/modal';
 *
 * @Component({
 *  selector: 'c8y-add-application-wizard',
 *  templateUrl: './templatePath'
 * })
 * export class AddApplicationWizardComponent {
 *   constructor(private wizardService: WizardService) {}
 *
 *   addApplication() {
 *     const wizardConfig: WizardConfig = {
 *       headerText: 'Add Application',
 *       headerIcon: 'c8y-icon-modules',
 *       bodyHeaderText: 'Select methods',
 *       bodyHeaderIcon: 'c8y-icon-modules'
 *     };
 *
 *     const initialState: Wizard = {
 *       wizardConfig,
 *       id: 'uploadApplication'
 *     };
 *
 *     const modalOptions: ModalOptions = { initialState };
 *
 *     this.wizardService.show(modalOptions);
 *   }
 * }
 * ```
 */

@Component({
  selector: 'c8y-wizard',
  templateUrl: './wizard.component.html'
})
export class WizardComponent<T = any> implements Wizard<T>, OnInit {
  @ViewChild('headerRef', { static: true }) header: TemplateRef<any>;
  @ViewChild('bodyRef', { static: true }) body: TemplateRef<any>;
  @ViewChild('footerRef', { static: true }) footer: TemplateRef<any>;
  /**
   * The id parameter identifies the particular wizard and can be used to refer to it when hooking wizard entries.
   * Example: "wizardId".
   */
  @Input() id: string;
  /**
   * A configuration object that allows you to set header values, both text and icon.
   */
  @Input() wizardConfig: WizardConfig = {};
  /**
   * A state that will be assigned to the actual component contained by the wizard.
   */
  componentInitialState: Partial<T> = {};
  /**
   * The event that is emitted when the wizard is closed.
   */
  @Output() onClose: EventEmitter<any> = new EventEmitter();
  /**
   * The event that is emitted when the wizard is reseted.
   */
  @Output() onReset: EventEmitter<any> = new EventEmitter();
  /**
   * The event that is emitted when the wizard entry is clicked.
   */
  @Output() onSelect: EventEmitter<WizardEntry> = new EventEmitter();
  readonly headerTemplate$ = this.wizardService.headerTemplate$.pipe(
    filter(header => header.wizardId === this.id)
  );
  readonly bodyTemplate$ = this.wizardService.bodyTemplate$.pipe(
    filter(body => body.wizardId === this.id)
  );
  readonly footerTemplate$ = this.wizardService.footerTemplate$.pipe(
    filter(footer => footer.wizardId === this.id)
  );

  constructor(public wizardService: WizardService, public bsModalRef: BsModalRef) {}

  ngOnInit() {
    this.emitTemplates();
  }

  /**
   * Resets the wizard.
   * @param result Custom result object which will be emitted via onReset output.
   */
  reset(result?: any) {
    this.onReset.emit(result);
    this.emitTemplates();
  }

  /**
   * Closes the wizard.
   * @param result Custom result object which will be emitted via onClose output.
   */
  close(result?: any) {
    this.onClose.emit(result);
    this.onClose.complete();
    this.bsModalRef.hide();
  }

  private emitTemplates() {
    setTimeout(() => {
      this.wizardService.headerTemplate$.next({ template: this.header, wizardId: this.id });
      this.wizardService.bodyTemplate$.next({ template: this.body, wizardId: this.id });
      this.wizardService.footerTemplate$.next({ template: this.footer, wizardId: this.id });
    });
  }
}
