import { Inject, Injectable, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, of, Subject, timer } from 'rxjs';
import { filter, mergeMap, shareReplay, take, takeUntil } from 'rxjs/operators';
import { ExtensionPoint, fromTriggerOnce, stateToFactory } from '../common/extension-hooks';
import { StateService } from '../common/state-service.abstract';
import {
  DynamicComponentDefinition,
  DynamicComponentExtension,
  HOOK_COMPONENTS,
  RESOLVING_COMPONENT_WAIT_TIME
} from './dynamic-component.model';

@Injectable({
  providedIn: 'root'
})
export class DynamicComponentService
  extends StateService
  implements ExtensionPoint<DynamicComponentExtension> {
  items$: Observable<DynamicComponentDefinition[]>;
  readonly refreshTrigger = new Subject();
  readonly state$ = new BehaviorSubject<Set<DynamicComponentDefinition>>(
    new Set<DynamicComponentDefinition>()
  );

  /**
   * Additional factories that can be added by plugins.
   */
  factories: DynamicComponentExtension[] = [];

  constructor(
    private injector: Injector,
    router: Router,
    @Inject(RESOLVING_COMPONENT_WAIT_TIME) waitTimeout: number
  ) {
    super();

    this.items$ = fromTriggerOnce<DynamicComponentDefinition>(router, this.refreshTrigger, [
      () => this.injector.get(HOOK_COMPONENTS, []),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(takeUntil(timer(waitTimeout)), shareReplay(1));
  }

  refresh() {
    this.refreshTrigger.next(1);
  }

  get state() {
    return this.state$.value;
  }

  /**
   * Returns a component by it's id as an observable.
   * @param id The id of the component.
   */
  getById(id) {
    return this.items$
      .pipe(
        mergeMap((cmps: DynamicComponentDefinition[]) => cmps),
        filter((cmp: DynamicComponentDefinition) => cmp.id === id),
        take(1)
      )
      .toPromise();
  }

  add(component: DynamicComponentDefinition) {
    this.state.add(component);
    this.emitNewState();
  }

  remove(component: DynamicComponentDefinition) {
    this.state.delete(component);
    this.emitNewState();
  }
}
