import { Injector, Injectable, InjectionToken } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { StateService } from '../common/state-service.abstract';
import { Action, ActionFactory } from './action.model';
import {
  fromTrigger,
  ExtensionPoint,
  stateToFactory,
  sortByPriority,
  ExtensionFactory
} from '../common/extension-hooks';
import { map, shareReplay } from 'rxjs/operators';

/**
 * An extension HOOK can use either a pure value:
 * ```typescript
 *  { provide: HOOK_X, useValue: { ...hookValue }, multi: true }
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  { provide: HOOK_X, useValue: [{ ...hookValues }], multi: true }
 * ```
 *
 * Or an ExtensionFactory which allows to define a get() function. This function
 * gets called on each navigation with the current route and can return values
 * async (observable or promise).
 * ```typescript
 *  { provide: HOOK_X, useFactory: { get: (route) => doSomethingAsync(route) }, multi: true }
 * ```
 */
export type ActionExtension = Action | Action[] | ExtensionFactory<Action>;

/**
 * A hook to add an action using the multi-provider extension concept.
 */
export const HOOK_ACTION = new InjectionToken('ActionFactory');

/**
 * A service which defines global actions.
 */
@Injectable({
  providedIn: 'root'
})
export class ActionService extends StateService implements ExtensionPoint<ActionExtension> {
  items$: Observable<Action[]>;
  factories: ActionExtension[] = [];
  readonly refreshTrigger = new Subject();
  readonly state$ = new BehaviorSubject<Set<Action>>(new Set<Action>());

  constructor(private injector: Injector, router: Router) {
    super();
    this.items$ = fromTrigger(router, this.refreshTrigger, [
      () => this.injector.get(HOOK_ACTION, []),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      map((items: Action[]) => sortByPriority(items)),
      shareReplay(1)
    );
  }

  /**
   * Refresh the extension factories
   */
  refresh() {
    this.refreshTrigger.next(1);
  }

  /**
   * Returns the current state.
   * @readonly
   * @returns The current set of actions.
   */
  get state(): Set<Action> {
    return this.state$.value;
  }

  /**
   * Adds a new item to the actions in the header and emits a state change.
   * @param item The item to add.
   */
  add(item: Action) {
    this.state.add(item);
    this.emitNewState();
  }

  /**
   * Removes an action from the header and emits a state change.
   * @param item The item to remove.
   */
  remove(item: Action) {
    this.state.delete(item);
    this.emitNewState();
  }
}
