import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { WizardComponent } from './wizard.component';
import { WizardService } from './wizard.service';

@Component({
  selector: 'c8y-wizard-footer',
  template: `
    <ng-template #footerContent>
      <div class="modal-footer">
        <ng-content></ng-content>
      </div>
    </ng-template>
  `
})
export class WizardFooterComponent implements OnInit {
  @ViewChild('footerContent', { static: true }) footerContent: TemplateRef<any>;

  constructor(private wizard: WizardComponent, private wizardService: WizardService) {}

  ngOnInit() {
    setTimeout(() => {
      this.wizardService.footerTemplate$.next({
        template: this.footerContent,
        wizardId: this.wizard.id
      });
    });
  }
}
