import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { WizardComponent } from './wizard.component';
import { WizardService } from './wizard.service';

@Component({
  selector: 'c8y-wizard-body',
  template: `
    <ng-template #bodyContent>
      <div class="modal-inner-scroll animated fadeIn">
          <ng-content></ng-content>
      </div>
    </ng-template>
  `
})
export class WizardBodyComponent implements OnInit {
  @ViewChild('bodyContent', { static: true }) bodyContent: TemplateRef<any>;

  constructor(private wizard: WizardComponent, private wizardService: WizardService) {}

  ngOnInit() {
    setTimeout(() => {
      this.wizardService.bodyTemplate$.next({
        template: this.bodyContent,
        wizardId: this.wizard.id
      });
    });
  }
}
