import { Pipe, PipeTransform } from '@angular/core';
import { entries, mapValues, omitBy } from 'lodash-es';
import { combineLatest, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { toObservable } from './extension-hooks';

@Pipe({ name: 'showIfFilter' })
export class ShowIfFilterPipe implements PipeTransform {
  transform<
    T extends {
      [key: string]: {
        showIf?: boolean | (() => boolean | Promise<boolean> | Observable<boolean>);
      };
    }
  >(value: T): Observable<Partial<T>> {
    const observableShowIfMap = mapValues(value, val => {
      if (typeof val.showIf === 'boolean') {
        return of(val.showIf);
      } else if (typeof val.showIf === 'function') {
        return toObservable(val.showIf());
      } else {
        return of(true);
      }
    }) as Partial<T>;

    return combineLatest(
      entries(observableShowIfMap).map(([key, val]) =>
        val.pipe(map(showIf => ({ key, showIf })))
      ) as Array<Observable<{ key: string; showIf: boolean }>>
    ).pipe(
      map((allShowIfs: Array<{ key: string; showIf: boolean }>) => {
        allShowIfs.forEach(showIf => (value[showIf.key].showIf = showIf.showIf));
        return omitBy(value, (v: { showIf: boolean }) => !v.showIf);
      })
    );
  }
}
