import { Injectable, Injector, TemplateRef } from '@angular/core';
import { Router } from '@angular/router';
import { BsModalRef, BsModalService, ModalOptions } from 'ngx-bootstrap/modal';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { ExtensionPoint, fromTrigger, stateToFactory } from '../common/extension-hooks';
import { StateService } from '../common/state-service.abstract';
import { WizardComponent } from './wizard.component';
import { HOOK_WIZARD, WizardEntry, WizardExtension } from './wizard.model';

@Injectable({
  providedIn: 'root'
})
export class WizardService extends StateService implements ExtensionPoint<WizardExtension> {
  /** Wizard list observable. It emits the current list of the wizard entries. */
  readonly items$: Observable<WizardEntry[]>;
  /** Wizard header subject. It emits header template. */
  readonly headerTemplate$: Subject<{ wizardId: string; template: TemplateRef<any> }> =
    new Subject();
  /** Wizard body subject. It emits body template. */
  readonly bodyTemplate$: Subject<{ wizardId: string; template: TemplateRef<any> }> = new Subject();
  /** Wizard footer subject. It emits footer template. */
  readonly footerTemplate$: Subject<{ wizardId: string; template: TemplateRef<any> }> =
    new Subject();
  /**
   * @ignore
   */
  readonly state$ = new BehaviorSubject<Set<WizardEntry>>(new Set<WizardEntry>());
  /** Refresh the extension factories subject. */
  readonly refreshTrigger = new Subject();
  /** Additional factories that can be added by plugins. */
  factories: WizardExtension[] = [];

  constructor(
    private bsModalService: BsModalService,
    private injector: Injector,
    private router: Router
  ) {
    super();
    this.items$ = fromTrigger(this.router, this.refreshTrigger, [
      () => this.injector.get(HOOK_WIZARD, []),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      map(entries => this.getUniqueListBy(entries, 'name', 'wizardId') as WizardEntry[]),
      shareReplay(1)
    );
  }

  /**
   * Returns the current state.
   * @returns The current set of entries.
   * @readonly
   */
  get state(): Set<WizardEntry> {
    return this.state$.value;
  }

  /**
   * Refresh the extension factories.
   */
  refresh() {
    this.refreshTrigger.next();
  }

  /**
   * Adds a new entry in the wizard.
   * @param entry Wizard entry to add.
   */
  add(entry: WizardEntry) {
    this.state.add(entry);
    this.emitNewState();
  }

  /**
   * Removes entry from the wizard.
   * @param entry Wizard entry to remove.
   */
  remove(entry: WizardEntry) {
    this.state.delete(entry);
    this.emitNewState();
  }

  /**
   * List of wizard entries matching the given id.
   * @param id
   * @returns observable with list of wizard entries.
   */
  getEntriesListById(id: string): Observable<WizardEntry[]> {
    return this.items$.pipe(map(entries => entries.filter(entry => entry.wizardId === id)));
  }

  /**
   * Shows a wizard modal.
   * @param modalOptions Modal options.
   * @returns a reference to the modal.
   */
  show(modalOptions: ModalOptions = {}): BsModalRef<WizardComponent> {
    const options = {
      class: 'modal-sm',
      backdrop: 'static',
      ...modalOptions
    } as ModalOptions<WizardComponent>;
    return this.bsModalService.show<WizardComponent>(WizardComponent, options);
  }

  private getUniqueListBy<T>(entries: T[], ...keys: string[]): T[] {
    return [
      ...new Map(entries.map(item => [this.getUniqIdentifierForKeys(item, keys), item])).values()
    ];
  }

  private getUniqIdentifierForKeys(entry: any, keys: string[]): string {
    return keys.map(key => entry[key]).join('_');
  }
}
