import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { distinctUntilChanged, filter, map, shareReplay, take } from 'rxjs/operators';
import {
  ExtensionFactory,
  ExtensionPoint,
  fromTrigger,
  stateToFactory,
} from '../common/extension-hooks';
import { StateService } from '../common/state-service.abstract';
import { Step } from './stepper.model';

/**
 * An extension HOOK can use either a pure value:
 * ```typescript
 *  { provide: HOOK_X, useValue: { ...hookValue }, multi: true }
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  { provide: HOOK_X, useValue: [{ ...hookValues }], multi: true }
 * ```
 *
 * Or an ExtensionFactory which allows to define a get() function. This function
 * gets called on each navigation with the current route and can return values
 * async (observable or promise).
 * ```typescript
 *  { provide: HOOK_X, useFactory: { get: (route) => doSomethingAsync(route) }, multi: true }
 * ```
 */
export type StepperExtension = Step | Step[] | ExtensionFactory<Step>;

/**
 * A hook to use for Multi Provider extension.
 */
export const HOOK_STEPPER = new InjectionToken<StepperExtension[]>('HookStepper');

/**
 * A service which defines the steps in a stepper component.
 * A stepper hook needs an certain outlet which has an matching id
 * to display the steps.
 */
@Injectable({
  providedIn: 'root',
})
export class StepperService extends StateService implements ExtensionPoint<StepperExtension> {
  items$: Observable<Step[]>;
  readonly refreshTrigger = new Subject();
  readonly state$ = new BehaviorSubject<Set<Step>>(new Set<Step>());
  factories: StepperExtension[] = [];

  constructor(
    private injector: Injector,
    router: Router,
    private translateService: TranslateService
  ) {
    super();
    this.items$ = fromTrigger(router, this.refreshTrigger, [
      () => this.injector.get(HOOK_STEPPER, []),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      shareReplay(1),
      distinctUntilChanged()
    ) as Observable<Step[]>;
  }

  sortSteps(steps: Step[]) {
    return steps.sort((a, b) => {
      const priorityOrder = (b.priority || 0) - (a.priority || 0);

      const aTranslatedLabel = this.translateService.instant(a.label);
      const bTranslatedLabel = this.translateService.instant(b.label);
      const translatedLabelOrder = aTranslatedLabel.localeCompare(bTranslatedLabel);

      return priorityOrder || translatedLabelOrder;
    });
  }

  /**
   * Refresh the extension factories
   */
  refresh() {
    this.refreshTrigger.next(1);
  }

  /**
   * Returns the current state.
   * @readonly
   * @returns The current set of actions.
   */
  get state(): Set<Step> {
    return this.state$.value;
  }

  /**
   * Adds a new item to tabs and emits state change
   * @param item The item to add.
   */
  add(item: Step) {
    this.state.add(item);
    this.emitNewState();
  }

  /**
   * Removes a step and emits a state change.
   * @param item The item to remove.
   */
  remove(item: Step) {
    this.state.delete(item);
    this.emitNewState();
  }

  /**
   * Returns the component by it's asssigned stepper id as an observable.
   * @param id The id of the stepper to hook into.
   */
  getById$(id) {
    return this.items$.pipe(
      filter((steps) => steps.length > 0),
      map((steps) => steps.filter((cmp) => cmp.stepperId === id)),
      take(1)
    );
  }
}
