import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import {
  ExtensionFactory,
  ExtensionPoint,
  fromTrigger,
  stateToFactory
} from '../common/extension-hooks';
import { StateService } from '../common/state-service.abstract';
import { Breadcrumb, BreadcrumbItem } from './breadcrumb.model';

/**
 * An extension HOOK can use either a pure value:
 * ```typescript
 *  { provide: HOOK_X, useValue: { ...hookValue }, multi: true }
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  { provide: HOOK_X, useValue: [{ ...hookValues }], multi: true }
 * ```
 *
 * Or an ExtensionFactory which allows to define a get() function. This function
 * gets called on each navigation with the current route and can return values
 * async (observable or promise).
 * ```typescript
 *  { provide: HOOK_X, useFactory: { get: (route) => doSomethingAsync(route) }, multi: true }
 * ```
 */
export type BreadcrumbExtension = Breadcrumb | Breadcrumb[] | ExtensionFactory<Breadcrumb>;

export const HOOK_BREADCRUMB = new InjectionToken('BreadcrumbFactory');

@Injectable({
  providedIn: 'root'
})
export class BreadcrumbService extends StateService implements ExtensionPoint<BreadcrumbExtension> {
  items$: Observable<Breadcrumb[]>;
  factories: BreadcrumbExtension[] = [];
  readonly refreshTrigger = new Subject();
  readonly state$ = new BehaviorSubject<Set<Breadcrumb>>(new Set<Breadcrumb>());
  private preferredPath: string;

  constructor(private injector: Injector, router: Router) {
    super();
    this.items$ = fromTrigger(router, this.refreshTrigger, [
      () => this.injector.get(HOOK_BREADCRUMB, []),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      map(breadcrumbs => this.sortByPreferredPath(breadcrumbs)),
      shareReplay(1)
    );
  }

  get state() {
    return this.state$.value;
  }

  /**
   * Adds a new item to the action bar in the header.
   * @param item The item to add.
   */
  add(item: Breadcrumb) {
    this.state.add(item);
    this.emitNewState();
  }

  /**
   * Removes an action bar item from the header.
   * @param item The item to remove.
   */
  remove(item: Breadcrumb) {
    this.state.delete(item);
    this.emitNewState();
  }

  /**
   * Refresh the extension factories
   */
  refresh() {
    this.refreshTrigger.next(1);
  }

  /**
   * Sets the crumb that contains that path to the top.
   * @param path The path to prefer.
   */
  selectPreferredByPath(path: string) {
    this.preferredPath = path;
    this.refresh();
  }

  sortByPreferredPath(breadcrumbs) {
    if (this.preferredPath) {
      return breadcrumbs.sort(bc =>
        bc.items.find((item: BreadcrumbItem) => !!item.path.match(this.preferredPath)) ? -1 : 1
      );
    }
    return breadcrumbs;
  }
}
