import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { RouteFactory, Route, ViewContext } from './router.models';
import { ContextRouteService } from './context-route.service';
import { IManagedObject } from '@c8y/client';
import { Observable, Subject } from 'rxjs';
import { startWith, switchMap } from 'rxjs/operators';
import { ExtensionFactory, ExtensionPoint, fromFactories } from '../common/extension-hooks';

declare const __MODE__: string;

/**
 * An extension HOOK can use either a pure value:
 * ```typescript
 *  { provide: HOOK_X, useValue: { ...hookValue }, multi: true }
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  { provide: HOOK_X, useValue: [{ ...hookValues }], multi: true }
 * ```
 *
 * Or an ExtensionFactory which allows to define a get() function. This function
 * gets called on each navigation with the current route and can return values
 * async (observable or promise).
 * ```typescript
 *  { provide: HOOK_X, useFactory: { get: (route) => doSomethingAsync(route) }, multi: true }
 * ```
 */
export type RouteExtension = Route | Route[] | ExtensionFactory<Route>;

/**
 * A hook to use for Multi Provider extension.
 * @deprecated: Use HOOK_ROUTE
 */
export const HOOK_ONCE_ROUTE = new InjectionToken('RouteFactory');

/**
 * A hook to use for Multi Provider extension.
 */
export const HOOK_ROUTE = new InjectionToken<RouteFactory>('HookRoute');

/**
 * A service which defines routes
 */
@Injectable({
  providedIn: 'root'
})
export class RouterService implements ExtensionPoint<RouteExtension> {
  items$: Observable<Route[]>;
  readonly refreshTrigger = new Subject();
  readonly state = new Map<string, Route>();

  factories: RouteExtension[] = [];

  constructor(
    private injector: Injector,
    public router: Router,
    private contextRouteService: ContextRouteService
  ) {
    this.refreshTrigger
      .pipe(
        startWith(1),
        switchMap(() =>
          fromFactories(
            [
              () => this.injector.get(HOOK_ROUTE, []),
              () => this.getComponentsViaDeprecatedHook(),
              () => this.factories
            ],
            router,
            false
          )
        )
      )
      .subscribe((routes: Route[]) => {
        const toAddRoutes = routes.filter(route => {
          const path = `${route.context || ''}${route.path}`;
          const isAlreadyAdded = this.state.has(path);
          if (!isAlreadyAdded) {
            this.state.set(path, route);
          }
          return !isAlreadyAdded;
        });
        this.addRoute(toAddRoutes);
      });
  }

  /**
   * Refresh the current context
   */
  refresh() {
    this.refreshTrigger.next(1);
    this.contextRouteService.refreshContext();
  }

  /**
   * Add a new route to the router configuration or a context.
   * @param route The route to add
   */
  addRoute(route: Route | Route[]) {
    this.addRoutes(Array.isArray(route) ? route : [route]);
  }

  /**
   * Determines if the given MO is an group or an device and returns
   * the correct href to link correctly to that MO.
   * @param groupOrDevice The MO of a group or the device.
   * @param prefix How should the link be prefixed.
   */
  getHref(groupOrDevice: IManagedObject, prefix = '#/'): string {
    if (groupOrDevice.c8y_IsDeviceGroup || groupOrDevice.c8y_IsDynamicGroup) {
      return `${prefix}${ViewContext.Group.replace(':id', groupOrDevice.id)}`;
    }
    return `${prefix}${ViewContext.Device.replace(':id', groupOrDevice.id)}`;
  }

  private getComponentsViaDeprecatedHook() {
    const componentsViaDeprecatedHook: RouteFactory[] = this.injector.get(
      HOOK_ONCE_ROUTE,
      []
    ) as RouteFactory[];
    if (
      componentsViaDeprecatedHook &&
      componentsViaDeprecatedHook.length &&
      __MODE__ !== 'production'
    ) {
      console.warn('HOOK_ONCE_ROUTE is now deprecated. Use HOOK_ROUTE instead.');
    }

    return componentsViaDeprecatedHook;
  }

  private addRoutes(routes) {
    const emptyRoute = this.router.config.find(r => r.path === '**');
    this.router.resetConfig(
      [
        ...this.router.config.filter(r => r.path !== '**'),
        ...routes.map((r: Route) => this.convertRoute(this.router.config, r)),
        emptyRoute
      ].filter(Boolean)
    );
  }

  private convertRoute(initialConfig, route: Route) {
    if (route.context) {
      initialConfig.forEach((r: Route) => {
        if (r.data && r.data.context === route.context) {
          r.children = [route, ...(r.children || [])];
        }
      });
      return;
    }
    return route;
  }
}
