import { FormlyFieldConfig } from '@ngx-formly/core';
import { TranslateParser, TranslateService } from '@ngx-translate/core';
import { combineLatest, isObservable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { gettext } from '../../i18n';

export class TranslateExtension {
  constructor(private translate: TranslateService, private parser: TranslateParser) {}
  prePopulate(field: FormlyFieldConfig) {
    const to = field.templateOptions || {};
    if (!to.label || to._translated) {
      return;
    }

    to._translated = true;
    field.expressionProperties = {
      ...(field.expressionProperties || {}),
      'templateOptions.label': to.label ? this.translate.stream(to.label) : to.label,
      'templateOptions.description': to.description
        ? this.translate.stream(to.description)
        : to.description,
      'templateOptions.placeholder': to.placeholder
        ? combineLatest([
            this.translate.stream(to.placeholder),
            this.translate.stream(gettext('e.g. {{ example }}'))
          ]).pipe(
            map(([placeholder, expression]) => this.parser.interpolate(expression, { example: placeholder }))
          )
        : to.placeholder,
      'templateOptions.options': (isObservable(to.options) ? to.options : of(to.options)).pipe(
        map((options: Array<{ label: string }>) =>
          (options || []).map(option => ({
            ...option,
            label: this.translate.instant(option.label)
          }))
        )
      )
    };
  }
}

export function registerTranslateExtension(translate: TranslateService, parser: TranslateParser) {
  return {
    extensions: [
      {
        name: 'translate',
        extension: new TranslateExtension(translate, parser)
      }
    ]
  };
}
