import { Component, Output, EventEmitter, Input, ViewChild } from '@angular/core';
import { ControlContainer, NgForm } from '@angular/forms';
import { LoginService } from '../login/login.service';
import { UserService } from '@c8y/client';
import { AlertService } from '../alert/alert.service';
import { ModalComponent } from '../modal/modal.component';

@Component({
  selector: 'c8y-totp-challenge',
  templateUrl: './totp-challenge.component.html',
  viewProviders: [ { provide: ControlContainer, useExisting: NgForm } ]
})

export class TotpChallengeComponent {
  /**
   * Calls the verify endpoint if set to true (default true)
   */
  @Input() verify = true;
  /**
   * Emits the token on success.
   */
  @Output() onSuccess = new EventEmitter();
  /**
   * Emits if set up two-factor authentication is canceled.
   */
  @Output() totpUnconfirmedEmitter = new EventEmitter();
  @Input() loading = false;
  @Input() hasError = false;
  model = {
    token: ''
  };
  @ViewChild('modal', { static: false }) private modal: ModalComponent;

  constructor(
    public loginService: LoginService,
    private users: UserService,
    private alert: AlertService
  ) {}

  async verifyCode() {
    try {
      this.loading = true;
      this.hasError = false;
      if (this.verify) {
        await this.users.verifyTotpCode(this.model.token);
      }
      this.onSuccess.emit(this.model.token);
    } catch (e) {
      this.hasError = true;
      this.alert.removeLastDanger();
      this.loading = false;
    }
  }

  cancel() {
    this.totpUnconfirmedEmitter.emit();
  }
}
