import { Component, ViewChild } from '@angular/core';
import { Subject } from 'rxjs';
import { Status, StatusType } from '../../../common/status.model';
import { gettext } from '../../../i18n/gettext';
import { ConfirmModalComponent } from '../../../modal/modal-confirm.component';
import { ModalLabels } from '../../../select-modal/select-modal.model';
import { Cookie, CookiePreferences } from '../cookie-banner.model';
import { CookieBannerService } from '../cookie-banner.service';

@Component({
  selector: 'c8y-cookie-banner-preferences-modal',
  templateUrl: './cookie-banner-preferences-modal.component.html'
})
export class CookieBannerPreferencesModalComponent {
  @ViewChild('modalRef', { static: false }) modalRef: ConfirmModalComponent;
  closeSubject: Subject<CookiePreferences> = new Subject();
  title = gettext('Cookie preferences');
  labels: ModalLabels = { ok: gettext('Save settings'), cancel: gettext('Cancel') };
  status: StatusType = Status.INFO;
  configCookiePreferences: CookiePreferences;
  configCookiePreferencesList: Cookie[];

  constructor(private cookieBannerService: CookieBannerService) {}

  ngOnInit() {
    this.configCookiePreferencesList = this.cookieBannerService.transformCookiePreferencesToList(
      this.configCookiePreferences
    );
  }

  async ngAfterViewInit() {
    try {
      await this.modalRef.result;
      this.onClose();
    } catch (error) {
      this.onDismiss();
    }
  }

  onClose() {
    const cookiePref: CookiePreferences = this.cookieBannerService.transformCookiePreferencesListToCookiePreferences(
      this.configCookiePreferencesList
    );

    this.closeSubject.next(cookiePref);
    this.closeSubject.complete();
  }

  onDismiss() {
    this.closeSubject.next(undefined);
    this.closeSubject.complete();
  }

  getCookieDescription(cookieType: 'required' | 'functional' | 'marketing'): string {
    return this.cookieBannerService.getCookieDescription(cookieType);
  }
}
