import { Component, OnInit } from '@angular/core';
import { ConfigOption, FieldArrayType } from '@ngx-formly/core';

@Component({
  selector: 'c8y-array-type',
  templateUrl: './array.type.component.html'
})
export class ArrayTypeComponent extends FieldArrayType implements OnInit {
  static readonly CONFIG: ConfigOption = {
    types: [{ name: 'array', component: ArrayTypeComponent }]
  };
  add() {
    this.formControl.markAsTouched();
    super.add();
  }

  remove(i: number) {
    this.formControl.markAsTouched();
    super.remove(i);
  }

  ngOnInit(): void {
    if (this.to.required && !this.field.fieldGroup.length) {
      this.add();
    }
  }
}
