import { Injectable } from '@angular/core';
import { IOperation } from '@c8y/client';
import { RealtimeSubjectService } from './realtime-subject.service';
import { RealtimeService } from './realtime.service';

/**
 * A service for handling bulk operation realtime notifications in RxJS fashion.
 */
@Injectable()
export class OperationRealtimeService extends RealtimeService<IOperation> {
  constructor(protected realtimeSubject: RealtimeSubjectService) {
    super(realtimeSubject);
  }

  protected channel(): string {
    return '/operations/*';
  }
}
