import { Component, Output, Input, EventEmitter, ViewChild, OnInit } from '@angular/core';
import { DroppedFile, DropAreaComponent } from '../drop-area/drop-area.component';
import { PickedFiles } from './file-picker.model';
import { isEmpty } from 'lodash-es';
import { FilePickerConfig } from './file-picker-config.model';

/**
 * User can upload a binary directly or use an URL.
 *
 * ## Usage Example:
 *
 * ```html
 * <div>
 * <c8y-file-picker [maxAllowedFiles]="1" (onFilesPicked)="onFile($event)">
 * </c8y-file-picker>
 * </div>
 * ```
 */

@Component({
  selector: 'c8y-file-picker',
  templateUrl: './file-picker.component.html'
})
export class FilePickerComponent implements OnInit {
  @ViewChild(DropAreaComponent, { static: true }) dropArea: DropAreaComponent;
  @Output() onFilesPicked: EventEmitter<PickedFiles> = new EventEmitter();
  @Input() maxAllowedFiles: number = Infinity;
  @Input() uploadChoice: 'uploadBinary' | 'uploadUrl' = 'uploadBinary';
  @Input() fileUrl: string;
  @Input() fileBinary: DroppedFile;
  @Input() config: FilePickerConfig = { maxlength: 2048 };
  /**
   * Additional string to be displayed in a popover.
   */
  @Input() fileUrlPopover: string;

  droppedFiles: DroppedFile[];
  private fileToSave: PickedFiles;

  /**
   * @ignore
   */
  ngOnInit(): void {
    if (this.fileBinary) {
      this.droppedFiles = [this.fileBinary];
    }
  }

  /**
   * Triggered by dropped file in component and pass it into drop-area component.
   */
  onFileDropped(droppedFiles: DroppedFile[]): void {
    this.onFilesPicked.emit({
      droppedFiles
    });
  }

  /**
   * Triggered when user changes upload choice, from upload url to upload binary.
   */
  clearInputFromUrl(): void {
    delete this.fileUrl;
    this.fileToSave = {
      url: undefined
    };
    this.onFilesPicked.emit(this.fileToSave);
  }

  /**
   * Triggered when user changes upload choice, to avoid cumulation of droppedFiles.
   */
  clearSelectedFiles(): void {
    this.dropArea.onDelete();
    this.fileToSave = {
      droppedFiles: null
    };
    this.onFilesPicked.emit(this.fileToSave);
  }

  /**
   * Triggered when user puts binary's url to upload.
   */
  onFileUrlChange(urlStr): void {
    this.fileToSave = {
      url: urlStr
    };
    this.onFilesPicked.emit(this.fileToSave);
  }

  /**
   * Checks if there is popover to display.
   */
  isPopoverUsed() {
    return !isEmpty(this.fileUrlPopover);
  }
}
