import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IResultList } from '@c8y/client';
import { Observable, pipe, Subscription } from 'rxjs';
import { map, tap } from 'rxjs/operators';
import { gettext } from '../i18n/gettext';
import { DisplayOption } from './list-display-switch.model';
import { Location } from '@angular/common';

@Component({
  selector: 'c8y-list-display-switch',
  templateUrl: './list-display-switch.component.html'
})
export class ListDisplaySwitchComponent implements OnInit {
  @Input() listKey: string;
  @Input('listLength')
  set listLength(val: number) {
    this._listLength = val;
    this.loadSelectedOption();
  }
  @Input() filterPipe = pipe(tap());
  @Output() onListClassChange: EventEmitter<string> = new EventEmitter<string>();
  selectedOption: DisplayOption;

  readonly DISPLAY_OPTIONS: DisplayOption[] = [
    {
      name: 'AUTO',
      value: 'auto',
      label: gettext('Auto'),
      default: true,
      getListClass: () => {
        const option =
          this._listLength > this.AUTO_GRID_ITEMS_LIMIT
            ? this.DISPLAY_OPTIONS.find((opt) => opt.name === 'LIST')
            : this.DISPLAY_OPTIONS.find((opt) => opt.name === 'GRID');
        return option.getListClass();
      }
    },
    {
      name: 'GRID',
      value: 'grid',
      label: gettext('Grid'),
      getListClass: () => 'interact-grid'
    },
    {
      name: 'LIST',
      value: 'list',
      label: gettext('List'),
      getListClass: () => 'interact-list'
    }
  ];
  private readonly AUTO_GRID_ITEMS_LIMIT = 12;
  private _listLength: number;
  constructor(private location: Location) {}

  ngOnInit() {
    this.loadSelectedOption();
  }

  onOptionSelect(option: DisplayOption) {
    const localStorageKey = this.getLocalStorageKey();
    if (option.default) {
      localStorage.removeItem(localStorageKey);
    } else {
      localStorage.setItem(localStorageKey, option.value);
    }
    this.loadSelectedOption();
  }

  private loadSelectedOption() {
    this.selectedOption = this.getLocalStorageOption() || this.getDefaultOption();
    this.onListClassChange.emit(this.selectedOption.getListClass());
  }

  private getLocalStorageOption(): DisplayOption {
    return this.DISPLAY_OPTIONS.find(
      (option) => option.value === localStorage.getItem(this.getLocalStorageKey())
    );
  }

  private getDefaultOption(): DisplayOption {
    return this.DISPLAY_OPTIONS.find((option) => option.default);
  }

  private getLocalStorageKey(): string {
    return `list-display-option[${this.getListKey()}]`;
  }

  private getListKey(): string {
    return this.listKey || this.location.path();
  }
}
