import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FetchClient, IResultList, Service } from '@c8y/client';
import { get } from 'lodash-es';
import { ProviderDefinition } from '../model/provider-definition.model';

@Injectable()
export class ProviderDefinitionsService extends Service<ProviderDefinition> {
  protected baseUrl: string;
  protected listUrl: string;

  constructor(client: FetchClient, activatedRoute: ActivatedRoute) {
    super(client);
    this.baseUrl = get(activatedRoute, 'snapshot.data.endpoint.definitionsEndpoint.baseUrl');
    this.listUrl = get(activatedRoute, 'snapshot.data.endpoint.definitionsEndpoint.listUrl');
  }

  async list(): Promise<IResultList<ProviderDefinition>> {
    return super.list();
  }
}
