import { Injectable } from '@angular/core';
import { AppStateService } from './ui-state.service';
import { IIdentified, IManagedObject, InventoryService } from '@c8y/client';
import { property } from 'lodash-es';
import { memoize } from './memoize.decorator';
import { IUser, UserService } from '@c8y/client';

@Injectable()
export class Permissions {
  constructor(
    private appState: AppStateService,
    private inventory: InventoryService,
    private user: UserService
  ) {}

  /**
   * Checks if the current user has write permissions for the given mo.
   * (either through global role, individual device permissions or via inventory roles).
   *
   * @param {array} roleIds Array of roles which the current user should have.
   * @param {IManagedObject | IIdentified} mo The managed object for which we are checking whether the user has access.
   */
  canEdit(roleIds, mo: IManagedObject | IIdentified) {
    return this.checkIfCanEdit(roleIds, mo);
  }

  hasRole(roleId: string) {
    const currentUser = this.appState.currentUser.value;
    if (!currentUser) {
      throw new Error('Roles can only be requested if the user is logged in.');
    }
    return this.user.hasRole(currentUser, roleId);
  }

  hasAllRoles(roleIds: string[]) {
    const currentUser = this.appState.currentUser.value;
    if (!currentUser) {
      throw new Error('Roles can only be requested if the user is logged in.');
    }
    return this.user.hasAllRoles(currentUser, roleIds);
  }

  hasAnyRole(roleIds: string[]) {
    const currentUser = this.appState.currentUser.value;
    if (!currentUser) {
      throw new Error('Roles can only be requested if the user is logged in.');
    }
    return this.user.hasAnyRole(currentUser, roleIds);
  }

  @memoize(property('id'))
  protected async checkIfOwner(mo) {
    const currentUserName = await this.appState.currentUser.value.userName;
    const { data, res } = await this.inventory.detail(mo.id);
    return currentUserName === data.owner;
  }

  @memoize(property('id'))
  protected checkWithRequest(mo) {
    const moName = mo.name;
    const moId = mo.id;
    const partialUpdateObject: Partial<IManagedObject> = {
      id: moId,
      name: moName
    };
    return this.inventory
      .update(partialUpdateObject)
      .then(success => {
        return true;
      })
      .catch(err => {
        return false;
      });
  }

  private async checkIfCanEdit(roleIds, mo) {
    if (await this.hasAnyRole(roleIds)) {
      return true;
    } else if (await this.checkIfOwner(mo)) {
      return true;
    } else if (await this.checkWithRequest(mo)) {
      return true;
    }
    return false;
  }
}
