import { FormlyFieldConfig } from '@ngx-formly/core';

interface BasicInputArrayFormConfigInterface {
  /**
   * sets form's key
   */
  key: string;

  /**
   * text inside button that adds additional fields
   */
  addText: string;

  /**
   * label displayed above fields
   */
  label: string;

  /**
   * placeholder attribute value
   */
  placeholder: string;

  /**
   * if provided, blue information icon will be rendered with onHover tooltip
   */
  tooltip?: string;

  /**
   * If provided with true value, generated field will not be required.
   */
  optional?: boolean;
}

/**
 *  Generates simple array formFieldConfig
 *   @returns FormlyFieldConfig[]
 */
export function getBasicInputArrayFormFieldConfig(config: BasicInputArrayFormConfigInterface): FormlyFieldConfig[] {
  return [
    {
      key: config.key,
      type: 'array',
      defaultValue: [''],
      templateOptions: {
        required: !config.optional,
        addText: config.addText,
      },
      fieldArray: {
        type: 'string',
        templateOptions: {
          required: !config.optional,
          label: config.label,
          tooltip: config.tooltip,
          placeholder: config.placeholder
        },
      },
    },
  ];
}
