import { Injectable } from '@angular/core';
import { ApplicationRemotePlugins, IApplication, IIdentified } from '@c8y/client';
import { ApplicationService } from '@c8y/ngx-components/api';
import { AppStateService } from '../common/ui-state.service';

type Application = IIdentified | IApplication | number | string;
type Package = Application;

@Injectable()
export class PluginsService {
  constructor(
    private applicationService: ApplicationService,
    private appState: AppStateService
  ) {}

  async listPackages() {
    const { data } = await this.applicationService.list({ pageSize: 2000 });
    return data.filter((app: any) => app.exports || (app.manifest && app.manifest.exports));
  }

  async listVersions(forPackage: Package) {
    const { data } = await this.applicationService.binary(forPackage).listPlugins();
    return data
      .map(plugin => ({ ...plugin.pluginPackage, version: plugin.pluginName }))
      .filter(plugin => plugin.exports);
  }

  async listInstalled(forApp: Application, flat = true) {
    const app = await this.getApplication(forApp);
    const c8yJson = await this.getCumulocityJsonFile(app);
    if (flat) {
      let flatList = [];
      for (const key in c8yJson.imports) {
        if (c8yJson.imports.hasOwnProperty(key)) {
          flatList = [
            ...flatList,
            ...c8yJson.imports[key].map(imp =>
              /@/.test(key) ? `${key}/${imp}` : `${key}@latest/${imp}`
            )
          ];
        }
      }
      return flatList;
    }
    return c8yJson.imports;
  }

  async addByName(addTo: Application, pluginName: string) {
    const name = pluginName.split('//').pop();
    const contextPath = pluginName.split('//').shift();
    const pkg = {
      contextPath
    };
    return this.add(addTo, pkg, name);
  }

  async removeByName(removeFrom: Application, pluginName: string) {
    const name = pluginName.split('/').pop();
    const contextPath = /@latest/.test(pluginName)
      ? pluginName.split('@').shift()
      : pluginName.split('/').shift();

    const pkg = {
      contextPath
    };
    return this.remove(removeFrom, pkg, name);
  }

  async add(addToApp: Application, fromPackage: Package, name: string) {
    return this.addOrRemove(addToApp, fromPackage, name, true);
  }

  async remove(removeFromApp: Application, fromPackage: Package, name: string) {
    return this.addOrRemove(removeFromApp, fromPackage, name, false);
  }

  async updateRemotesInCumulocityJson(app: IApplication, remotes: ApplicationRemotePlugins) {
    const c8yJson = await this.getCumulocityJsonFile(app);
    return this.applicationService.storeAppManifest(app, { ...c8yJson, remotes });
  }

  private async addOrRemove(addToApp: Application, fromPackage: Package, name: string, add = true) {
    const application = await this.getApplication(addToApp);
    const pkg = await this.getApplication(fromPackage);
    const c8yJson = await this.getCumulocityJsonFile(application);

    // TODO: Versions?!!
    const pkgImport = new Set(c8yJson.imports[pkg.contextPath]);
    if (add) {
      pkgImport.add(name);
    } else {
      pkgImport.delete(name);
    }
    c8yJson.imports[pkg.contextPath] = Array.from(pkgImport);

    await this.applicationService.storeAppManifest(application, c8yJson);
    return c8yJson;
  }

  private async getCumulocityJsonFile(app: IApplication) {
    const c8yJson = await this.applicationService.getAppManifest(app);
    if (!c8yJson.imports) {
      c8yJson.imports = {};
    }
    return c8yJson;
  }

  private async getApplication(app: Application): Promise<IApplication> {
    if (typeof app !== 'number' && (app as IApplication).contextPath) {
      return app as IApplication;
    }
    const { data } = await this.applicationService.detail(app);
    return data;
  }
}
