import { Injectable, InjectionToken, Injector } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, Subject } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import {
  ExtensionFactory,
  ExtensionPoint,
  fromTriggerOnce,
  sortByPriority,
  stateToFactory
} from '../common/extension-hooks';
import { StateService } from '../common/state-service.abstract';
import { NavigatorNode } from './navigator-node';
import { NavigatorNodeData } from './navigator-node-data';
import { NavigatorNodeRoot } from './navigator-node-root';

/**
 * An extension HOOK can use either a pure value:
 * ```typescript
 *  { provide: HOOK_X, useValue: { ...hookValue }, multi: true }
 * ```
 *
 * Or an array to directly register multiple:
 * ```typescript
 *  { provide: HOOK_X, useValue: [{ ...hookValues }], multi: true }
 * ```
 *
 * Or an ExtensionFactory which allows to define a get() function. This function
 * gets called on each navigation with the current route and can return values
 * async (observable or promise).
 * ```typescript
 *  { provide: HOOK_X, useFactory: { get: (route) => doSomethingAsync(route) }, multi: true }
 * ```
 */
type NavigatorExtension = NavigatorNode | NavigatorNode[] | ExtensionFactory<NavigatorNode>;

/**
 * A hook to use for Multi Provider extension.
 */
export const HOOK_NAVIGATOR_NODES = new InjectionToken<NavigatorExtension[]>(
  'NavigatorNodesFactory'
);

/**
 * A service which defines the navigator.
 */
@Injectable({
  providedIn: 'root'
})
export class NavigatorService extends StateService implements ExtensionPoint<NavigatorExtension> {
  /**
   * Navigator menu items observable. It emits the current list of nodes in the navigator.
   */
  items$: Observable<NavigatorNode[]>;

  /**
   * Refresh the extension factories subject.
   * @readonly
   */
  readonly refreshTrigger = new Subject();

  /**
   * @ignore
   */
  readonly state$ = new BehaviorSubject<Set<NavigatorNode>>(new Set<NavigatorNode>());

  /**
   * Indicates whether the menu entry associated with the given URL should be expanded.
   */
  firstUrl = true;

  /**
   * Additional factories that can be added by plugins.
   */
  factories: NavigatorExtension[] = [];

  constructor(private injector: Injector, private router: Router) {
    super();
    const rootNode = new NavigatorNodeRoot();
    this.items$ = fromTriggerOnce(router, this.refreshTrigger, [
      () => this.injector.get(HOOK_NAVIGATOR_NODES, []),
      () => this.factories,
      stateToFactory(this.state$)
    ]).pipe(
      map((nodes: Partial<NavigatorNodeData[]>) => {
        const noParent = nodes.filter(node => !node.parent);
        const withParent = nodes.filter(node => node.parent);

        // Based on the sortedNodes array, the nodes are sequentially created.
        // Nodes sorting is done in two steps to have the top-level nodes first.
        // This way, by the time we are adding a child node, the parent node is already present.
        const sortedNodes = sortByPriority(noParent).concat(sortByPriority(withParent));
        rootNode.empty();
        sortedNodes.forEach(node => rootNode.addRoot(node));
        return rootNode.children;
      }),
      shareReplay(1)
    );
  }

  /**
   * Checks whenever a navigator entry associated with the given URL should be expanded.
   * @param {NavigatorNode} node Navigator node.
   */
  openOnFirstUrl(node: NavigatorNode) {
    if (this.firstUrl && !!node.path) {
      return (
        this.router.isActive(node.path, node.routerLinkExact) || node.openOnStart(this.router.url)
      );
    }
    return false;
  }

  /**
   * Returns the current state.
   * @readonly
   * @returns The current set of actions.
   */
  get state(): Set<NavigatorNode> {
    return this.state$.value;
  }

  /**
   * Refresh the extension factories.
   */
  refresh() {
    this.refreshTrigger.next(1);
  }

  /**
   * Adds a new node to the navigator.
   * @param {NavigatorNode} node Navigator node to add.
   */
  add(node: NavigatorNode) {
    this.state.add(node);
    this.emitNewState();
  }

  /**
   * Removes a node from the navigator.
   * @param {NavigatorNode} node Navigator node to remove.
   */
  remove(node: NavigatorNode) {
    this.state.delete(node);
    this.emitNewState();
  }
}
