import { Inject, Injectable, Injector, Optional } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { flatten } from 'lodash-es';
import { forkJoin, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { toObservable } from '../common';
import { Tab, TabFactory } from '../tabs';
import { DynamicProviderConfig } from './model/dynamic-provider-config.model';
import { HOOK_DYNAMIC_PROVIDER_CONFIG } from './provider-configuration-hook';

@Injectable()
export class ProviderConfigurationTabFactory implements TabFactory {
  private config: DynamicProviderConfig[];

  constructor(
    @Optional()
    @Inject(HOOK_DYNAMIC_PROVIDER_CONFIG)
    config: DynamicProviderConfig[][],
    public router: Router,
    private injector: Injector
  ) {
    this.config = flatten(config);
  }

  get() {
    if (!this.config || !this.config.length) {
      return;
    }

    const configForRoute = this.config.find(c =>
      c.tab
        ? this.router.url === '/' + this.getNodeTabPath(c.navigation.path, c.tab.path) ||
          this.router.url.startsWith('/' + c.navigation.path.replace(/^\/|\/$/g, ''))
        : false
    );

    const filteredRoutes = configForRoute
      ? this.config.filter(
          c =>
            c.navigation.path.replace(/^\/|\/$/g, '') ===
              configForRoute.navigation.path.replace(/^\/|\/$/g, '') && c.tab
        )
      : [];

    const canActivate: Array<Observable<boolean>> = filteredRoutes
      .map(c =>
        c.tab.canActivate && c.tab.canActivate.length
          ? c.tab.canActivate.map(ca => this.injector.get(ca))
          : undefined
      )
      .map(this.checkCanActivate.bind(this));

    return canActivate.length > 0
      ? forkJoin(canActivate).pipe(
          map((canActivateResult: boolean[]) =>
            filteredRoutes
              .map((c, index) => {
                const tab = {
                  ...c.tab,
                  path: this.getNodeTabPath(c.navigation.path, c.tab.path)
                };
                return canActivateResult[index] ? (tab as Tab) : undefined;
              })
              .filter(el => !!el)
          )
        )
      : [];
  }

  private checkCanActivate(ca: CanActivate[]): Observable<boolean> {
    if (!!ca && ca.length) {
      const canActivateResult: Array<Observable<boolean>> = ca
        .map((canActivate: CanActivate) => canActivate.canActivate(undefined, undefined))
        .map(toObservable) as Array<Observable<boolean>>;

      return forkJoin(canActivateResult).pipe(
        map((caResult: boolean[]) => caResult.reduce((acc, curr) => acc && curr))
      );
    }
    return of(true);
  }

  private getNodeTabPath(nodePath, tabPath) {
    return `${nodePath.replace(/^\/|\/$/g, '')}/${tabPath.replace(/^\/|\/$/g, '')}`;
  }
}
