import { Component, Input } from '@angular/core';
import { gettext } from '../i18n/gettext';
import { RealtimeService } from './realtime.service';

@Component({
  selector: 'c8y-realtime-btn',
  templateUrl: './realtime-button.component.html'
})
export class RealtimeButtonComponent {
  @Input()
  service: RealtimeService<any>;
  @Input()
  label: string = gettext('Realtime');
  @Input()
  title: string = gettext('Toggle realtime');

  get active(): boolean {
    return !!this.service && this.service.active;
  }

  toggle() {
    if (this.service) {
      this.active ? this.service.stop() : this.service.start();
    }
  }
}
