import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FetchClient, IIdentified, IResult, Service } from '@c8y/client';
import { get } from 'lodash-es';
import { ProviderProperties } from '../model/provider-properties.model';

@Injectable()
export class ProviderConfigurationService extends Service<ProviderProperties> {
  private static readonly NO_DETAIL_ID: string = null;

  protected baseUrl: string;
  protected listUrl: string;

  constructor(client: FetchClient, activatedRoute: ActivatedRoute) {
    super(client);
    this.baseUrl = get(activatedRoute, 'snapshot.data.endpoint.configurationEndpoint.baseUrl');
    this.listUrl = get(activatedRoute, 'snapshot.data.endpoint.configurationEndpoint.listUrl');
  }

  async detail(): Promise<IResult<ProviderProperties>> {
    return super.detail(ProviderConfigurationService.NO_DETAIL_ID);
  }

  async update(entity: Partial<ProviderProperties>): Promise<IResult<ProviderProperties>> {
    return super.update(entity);
  }

  async delete(): Promise<IResult<null>> {
    return super.delete(ProviderConfigurationService.NO_DETAIL_ID);
  }

  /** There is a single provider configuration per tenant and detail URL is not needed */
  protected getDetailUrl(_: string | number | IIdentified): string {
    return this.listUrl;
  }
}
