import { Component, Input, ViewChild } from '@angular/core';
import { ActionBarItem } from './action-bar.model';
import { ActionBarService } from './action-bar.service';

/**
 * A <c8y-action-bar-item> can be added with content projection.
 * It allows to show items in three positions (left, right
 * or more).
 *
 * @example
 * ```html
 * <c8y-action-bar-item [placement]="'right'">
 *   <button class="btn btn-link" (click)="addRandom()"><i c8yIcon="plus-circle"></i> Add random</button>
 * </c8y-action-bar-item>
 * ```
 */
@Component({
  selector: 'c8y-action-bar-item',
  templateUrl: './action-bar-item.component.html'
})
export class ActionBarItemComponent implements ActionBarItem {
  /**
   * Where the item should be shown. Use "more" to place
   * it in a dropdown on the right.
   */
  @Input() placement: 'left' | 'right' | 'more' = 'left';

  /**
   * Priority for sorting the list. Higher priority means
   * an earlier position in the list.
   */
  @Input() priority: number = 0;

  /**
   * Adds additional classes to the item.
   */
  @Input() itemClass = '';

  /**
   * @ignore
   */
  @ViewChild('template', { static: false }) template: any;

  private viewInitTimeout;

  /**
   * @ignore
   */
  constructor(private actionBarService: ActionBarService) {}

  /**
   * @ignore
   */
  ngAfterViewInit() {
    this.viewInitTimeout = setTimeout(() => this.actionBarService.add(this as ActionBarItem));
  }

  /**
   * @ignore
   */
  ngOnDestroy() {
    clearTimeout(this.viewInitTimeout);
    this.actionBarService.remove(this as ActionBarItem);
  }
}
