import { Pipe, PipeTransform } from '@angular/core';
import { ActionControl } from './data-grid.model';

@Pipe({ name: 'isControlVisible' })
export class IsControlVisiblePipe implements PipeTransform {
  transform(actionControl: ActionControl, arg?: any): boolean {
    return !actionControl.showIf || actionControl.showIf(arg);
  }
}
