import { Observable, throwError } from 'rxjs';
import { concatMap, map, switchMap } from 'rxjs/operators';

declare const zip: any;

export class ZipService {
  getEntries(zipFile: File): Observable<ZipEntry[]> {
    return new Observable(subscriber => {
      const reader = new zip.BlobReader(zipFile);
      zip.createReader(
        reader,
        zipReader => {
          zipReader.getEntries(entries => {
            subscriber.next(entries);
            subscriber.complete();
          });
        },
        message => {
          subscriber.error({ message });
        }
      );
    });
  }

  getData(entry: ZipEntry): Observable<Blob> {
    const data = new Observable<Blob>(subscriber => {
      const writer = new zip.BlobWriter();

      (entry as any).getData(writer, blob => {
        subscriber.next(blob);
        subscriber.complete();
      });
    });
    return data;
  }

  getJsonData(zipFile: File, fileEntryFilter: { filename: string }): Observable<string> {
    return this.getEntries(zipFile).pipe(
      map(entries => {
        const entryFile = entries.find(entry => entry.filename === fileEntryFilter.filename);
        if (!entryFile) {
          throwError(`File not found ${JSON.stringify(fileEntryFilter)}`);
        }
        return entryFile;
      }),
      concatMap(entry => this.getData(entry)),
      switchMap(async blob => await blob.text()),
      map(text => JSON.parse(text))
    );
  }
}

export interface ZipEntry {
  version: number;
  bitFlag: number;
  compressionMethod: number;
  lastModDateRaw: number;
  lastModDate: string;
  crc32: number;
  compressedSize: number;
  uncompressedSize: number;
  filenameLength: number;
  extraFieldLength: number;
  commentLength: number;
  directory: boolean;
  offset: 0;
  filename: string;
  comment: string;
}
