import { Component } from '@angular/core';
import { IApplication, ICurrentTenant } from '@c8y/client';
import { get } from 'lodash-es';
import { Observable, of } from 'rxjs';
import { distinctUntilChanged, map, switchMap } from 'rxjs/operators';
import { ActionBarService } from '../action-bar/action-bar.service';
import { OptionsService } from '../common/options.service';
import { Permissions } from '../common/permissions.service';
import { AppStateService } from '../common/ui-state.service';
import { HeaderService } from '../header/header.service';
import { NavigatorService } from '../navigator/navigator.service';
import { PluginsResolveService } from '../plugins/plugins-resolve.service';
import { NEEDED_ROLE_FOR_SETUP, SetupStep, SETUP_FINISHED_STEP_ID } from '../setup/setup.model';
import { Steppers } from '../stepper/stepper.model';
import { StepperService } from '../stepper/stepper.service';
import { TabsService } from '../tabs/tabs.service';

@Component({
  selector: 'c8y-bootstrap',
  templateUrl: './bootstrap.template.html'
})
export class BootstrapComponent {
  navigatorOpen$: Observable<boolean>;
  noAppsMargin$: Observable<boolean>;
  tabsOrientation: string;
  showPoweredBy: boolean = true;
  isSetupNeeded$: Observable<boolean>;

  constructor(
    public tabs: TabsService,
    public ui: AppStateService,
    public navigator: NavigatorService,
    public actionBar: ActionBarService,
    public headerService: HeaderService,
    private options: OptionsService,
    private pluginsResolve: PluginsResolveService,
    private stepperService: StepperService,
    private permissions: Permissions
  ) {
    this.noAppsMargin$ = this.headerService.map(({ nav }) => !nav.open && nav.hiddenOnStartup);
    this.tabsOrientation = this.options.tabsHorizontal ? 'horizontal' : 'vertical';
    this.ui
      .map(({ lang }) => lang)
      .pipe(distinctUntilChanged())
      .subscribe(() => {
        this.actionBar.refresh();
      });
    this.showPoweredBy = !this.options.get('hidePowered');
    this.isSetupNeeded$ = this.ui.currentApplication.pipe(
      map(app => this.ui.isOwnerOfApplication(app)),
      switchMap(isOwner => {
        if (!isOwner) {
          return of([]);
        }
        return this.stepperService.getById$(Steppers.SETUP);
      }),
      map((steps: SetupStep[]) => this.getNotCompletedSetupSteps(steps)),
      map(
        notCompletedSetupSteps => this.options.forceSetup || this.options.isSetup && this.needsSetup(notCompletedSetupSteps)
      )
    );
  }

  async ngOnInit() {
    const { remoteModules } = (window as any).C8Y_APP;
    if (remoteModules.length) {
      await this.pluginsResolve.resolveRemotePlugins(remoteModules);
    }
  }

  private needsSetup(notCompletedSetupSteps: SetupStep[]): boolean {
    notCompletedSetupSteps = this.excludeSetupFinishStep(notCompletedSetupSteps);
    if (this.hasPermission()) {
      return notCompletedSetupSteps.length > 0;
    }
    // Show setup screen as there are required steps but the user
    // has no permission. The Setup screen will show an error to
    // the user.
    return this.hasRequiredSteps(notCompletedSetupSteps);
  }

  private excludeSetupFinishStep(notCompletedSetupSteps: SetupStep[]) {
    return notCompletedSetupSteps.filter(({ setupId }) => setupId !== SETUP_FINISHED_STEP_ID);
  }

  private hasRequiredSteps(notCompletedSetupSteps: SetupStep[]): boolean {
    return notCompletedSetupSteps.some(step => step.required);
  }

  private hasPermission(): boolean {
    return this.permissions.hasRole(NEEDED_ROLE_FOR_SETUP);
  }

  private getNotCompletedSetupSteps(steps: SetupStep[]): SetupStep[] {
    if (!this.ui.currentApplicationConfig.value) {
      return steps;
    }
    return steps.filter(
      step =>
        step.setupId && !(this.ui.currentApplicationConfig.value.setup || []).includes(step.setupId)
    );
  }
}
