import { Injectable } from '@angular/core';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { FormlyJsonschema } from '@ngx-formly/core/json-schema';
import { JSONSchema7 } from 'json-schema';

@Injectable({
  providedIn: 'root'
})
export class C8yJSONSchema extends FormlyJsonschema {
  toFieldConfig(schema: JSONSchema7, options?: any): FormlyFieldConfig {
    const config: FormlyFieldConfig = super.toFieldConfig(
      schema,
      this.c8yOptions((options || {}).map)
    );
    if (Array.isArray(config.fieldGroup)) {
      config.fieldGroup.forEach((field: FormlyFieldConfig) => {
        if (field.type === 'array' && field.templateOptions.required) {
          field.fieldArray.templateOptions.required = true;
        }
      });
    }
    return config;
  }

  private c8yOptions(
    map: (mappedField: FormlyFieldConfig, mapSource: JSONSchema7) => FormlyFieldConfig
  ) {
    return {
      map(mappedField: FormlyFieldConfig, mapSource: JSONSchema7) {
        let result: FormlyFieldConfig = mappedField;

        /** `const` type is represented as hidden input */
        if (mapSource.const) {
          result = {
            ...result,
            defaultValue: mapSource.const,
            templateOptions: { ...result.templateOptions, type: 'hidden' }
          };
        }

        /** `writeOnly` field represented as password input */
        if (mapSource.writeOnly) {
          result = {
            ...result,
            templateOptions: { ...result.templateOptions, type: 'password', autocomplete: 'off' }
          };
        }

        /** `examples` values used as placeholder */
        if (mapSource.examples) {
          result = {
            ...result,
            templateOptions: {
              ...result.templateOptions,
              placeholder: (mapSource.examples as string[]).join(', ')
            }
          };
        }

        /** `enum` field represented as radio button group */
        if (mapSource.enum) {
          result = {
            ...result,
            type: 'radio'
          };
        }

        /** fields with `contentMediaType` or `contentEncoding` are represented as file inputs */
        if (mapSource.contentMediaType || mapSource.contentEncoding) {
          result = {
            ...result,
            type: 'file',
            templateOptions: {
              ...result.templateOptions,
              accept: mapSource.contentMediaType,
              maxAllowedFiles: 1,
              contentEncoding: mapSource.contentEncoding
            }
          };
        }

        /** file extension validation for fields with `allowedFileTypes` */
        if ((mapSource as any).allowedFileTypes) {
          result = {
            ...result,
            templateOptions: {
              ...result.templateOptions,
              ...((mapSource as any).allowedFileTypes && {
                accept: (mapSource as any).allowedFileTypes.join(',')
              })
            }
          };
        }

        /** Provides a way to use templateOptions in JSONSchema forms */
        if ((mapSource as any).templateOptions) {
          result = {
            ...result,
            templateOptions: {
              ...(mapSource as any).templateOptions,
              ...result.templateOptions,
            }
          };
        }

        return map ? map(result, mapSource) : result;
      }
    };
  }
}
