import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BehaviorSubject } from 'rxjs';
import { OptionsService } from '../common/options.service';

@Injectable({ providedIn: 'root' })
export class HelpService {
  readonly contextHelp: BehaviorSubject<boolean | string> = new BehaviorSubject(false);
  isWarningAlertViewed: boolean = false;
  private readonly SUPPORTED_LANGUAGES = ['en'];

  constructor(private translateService: TranslateService, private options: OptionsService) {
    this.handleContextHelp();
  }

  isSupportedLanguage(): boolean {
    return this.SUPPORTED_LANGUAGES.indexOf(this.translateService.currentLang) > -1;
  }

  private handleContextHelp() {
    const option = this.getContextHelpOption();
    if (option) {
      this.contextHelp.next(option);
      return;
    }
    this.contextHelp.next(false);
  }

  private getContextHelpOption(): boolean | string {
    return this.options.contextHelp;
  }
}
