import { ChangeDetectionStrategy, Component } from '@angular/core';
import { ConfigOption, FieldType } from '@ngx-formly/core';

@Component({
  selector: 'c8y-field-input',
  templateUrl: './input.type.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FieldInput extends FieldType {
  static readonly CONFIG: ConfigOption = {
    types: [
      {
        name: 'input',
        component: FieldInput,
        wrappers: ['c8y-form-field']
      },
      { name: 'string', extends: 'input' },
      { name: 'typeahead', extends: 'input' },
      {
        name: 'number',
        extends: 'input',
        defaultOptions: {
          templateOptions: {
            type: 'number'
          }
        }
      },
      {
        name: 'integer',
        extends: 'input',
        defaultOptions: {
          templateOptions: {
            type: 'number'
          }
        }
      }
    ]
  };

  get type() {
    return this.to.type || 'text';
  }
}
